import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-checkbox';
import '@servicenow/now-text-link';
import '@servicenow/now-select';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

import '../sn-table-choices';

import { serializeSNName } from '../../../helpers/sn-naming-helpers';
import styles from './sn-table-field-card.scss';

import {
	FIELD_TYPES,
	FIELD_CHANGED,
	FIELD_DELETED,
	TYPE_UPDATED,
	REFERENCE_UPDATED,
	NONE_CHOICE_UPDATED,
	INVALID_SET,
	REQUIRED_ERR_MESSAGE,
	CHAR_LIMIT_MIN_ERR_MESSAGE,
	CHAR_LIMIT_MAX_ERR_MESSAGE,
	CHAR_INVALID_ERR_MESSAGE,
	CHOICE_TYPES,
	FIELD_INPUTTED
} from '../sn-table-field-list/constants';

import {
	CHOICES_UPDATED,
	CHOICE_ADDED,
	CHOICE_DELETED,
	CHOICE_EDITED
} from '../sn-table-choices/constants';

import { PREVENT_ONLY_WHITESPACE_REGEX } from '../../helpers/error-constants';

import validationHelpers from '../../helpers/table-field-validations';
const { isAttrInvalid } = validationHelpers;

import { tablesDataBehavior } from '@devsnc/guided-wizard-toolkit';
const {
	constants: { TABLES_DATA_PROPERTY }
} = tablesDataBehavior;

const renderConditionalInput = ({ field, errors = {}, allTables = [] }) => {
	switch (field.type) {
		case 'string':
			return (
				<now-input
					className="additional-input length"
					name="length"
					type="number"
					label={t('Character limit')}
					helper-content={t(
						'Max length of the strings that can be stored here.'
					)}
					value={field.length}
					min={1}
					max={8000}
					step={1}
					required
					manage-invalid
					invalid={isAttrInvalid(errors, 'length')}
					messages={
						isAttrInvalid(errors, 'length')
							? [
									{
										status: 'critical',
										content: errors.length.message
									}
							  ]
							: []
					}
				/>
			);
		case 'reference': {
			const isTablesLoading = allTables.length === 0;
			return (
				<sn-aow-typeahead
					className="additional-input reference"
					search="contains"
					label={t('Reference table')}
					name="reference"
					placeholder={isTablesLoading ? t('Loading...') : ''}
					helperContent={generateStyleToolTipContent(
						t(
							'Name of the table that is providing data to be used in this column.'
						)
					)}
					disabled={isTablesLoading}
					items={allTables}
					selectedItem={field.reference || ''}
					variant="tertiary"
					itemsLimit={100}
				/>
			);
		}
		case 'choice':
			return (
				<now-select
					className="additional-input"
					label={t('Choice type')}
					append-to-payload={{ name: 'choice' }}
					items={CHOICE_TYPES}
					selectedItem={'1'}
					size="md"
					variant="tertiary"
					select="single"
					helperContent={generateStyleToolTipContent(
						t('Defines the dropdown behavior of the choice list on a form.')
					)}
					manage-invalid
					invalid={isAttrInvalid(errors, 'choice')}
					messages={
						isAttrInvalid(errors, 'choice')
							? [{ status: 'critical', content: errors.choice.message }]
							: []
					}
				/>
			);
		default:
			return '';
	}
};

const renderChoiceList = field => {
	return field.type === 'choice' ? <sn-table-choices field={field} /> : null;
};

createCustomElement('sn-table-field-card', {
	view: (state, { dispatch, updateState }) => {
		const {
			optionsExpanded,
			properties: { field, errors }
		} = state;

		const { name, label, type, mandatory, display } = field;

		const typeaheadType = !type
			? 'none'
			: (FIELD_TYPES.find(f => f.id === type) || { id: 'none' }).id;

		return (
			<fieldset className="table-field-card-container">
				<legend>{t('Define a field on the table')}</legend>
				<div className="main-content">
					<div className="inputs-wrapper">
						<now-input
							className="label"
							name="label"
							value={label}
							label={t('Field label')}
							helper-content={t(
								'Create a unique label for the table column, such as "Name" or "Location"'
							)}
							required
							maxlength={80}
							pattern={PREVENT_ONLY_WHITESPACE_REGEX}
							manage-invalid
							invalid={isAttrInvalid(errors, 'label')}
							messages={
								isAttrInvalid(errors, 'label')
									? [{ status: 'critical', content: errors.label.message }]
									: []
							}
						/>
						<now-input
							className="name"
							name="name"
							value={name}
							label={t('Field name')}
							helper-content={t(
								'A name is created automatically after you enter a label. You can edit this name, if needed.'
							)}
							required
							maxlength={80}
							manage-invalid
							invalid={isAttrInvalid(errors, 'name')}
							messages={
								isAttrInvalid(errors, 'name')
									? [{ status: 'critical', content: errors.name.message }]
									: []
							}
						/>
						<now-select
							label={t('Type')}
							className="type"
							items={FIELD_TYPES}
							selected-item={typeaheadType}
							append-to-payload={{ name: 'type' }}
							variant="tertiary"
							helper-content={t(
								'Select the type of information that is stored in this column, such as text, dates, or references to other tables.'
							)}
						/>
						{renderConditionalInput(state.properties)}
					</div>

					<div className="now-align -block-center">
						<now-button-iconic
							className="expand"
							bare
							icon={optionsExpanded ? 'chevron-up-fill' : 'chevron-down-fill'}
							on-click={() =>
								updateState({ optionsExpanded: !optionsExpanded })
							}
							config-aria={{
								'aria-label': t('Expand options')
							}}
						/>
					</div>
				</div>
				{optionsExpanded ? (
					<div className="options-container">
						<div className="expanded-options">
							<div className="now-align">
								<div className="checkbox-wrapper">
									<sn-aow-checkbox-tooltip
										helperContent={generateStyleToolTipContent(
											t(
												'Option to use this column as the display value for the table. If another table references this table, the information from this column is what appears in the other table.'
											)
										)}
										constrain={{ maxWidth: 500 }}>
										<now-checkbox
											slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
											className="display"
											name="display"
											checked={display}
											label={t('Display')}
										/>
									</sn-aow-checkbox-tooltip>
								</div>
								<div className="checkbox-wrapper">
									<sn-aow-checkbox-tooltip
										helperContent={generateStyleToolTipContent(
											t(
												'Option to require that users fill in this column when updating the table.'
											)
										)}>
										<now-checkbox
											slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
											className="mandatory"
											name="mandatory"
											checked={mandatory}
											label={t('Mandatory')}
										/>
									</sn-aow-checkbox-tooltip>
								</div>
							</div>
							<now-button-iconic
								className="delete"
								bare
								icon="trash-fill"
								on-click={() => dispatch(FIELD_DELETED, { id: field.id })}
								variant="secondary"
							/>
						</div>
						{renderChoiceList(field)}
					</div>
				) : null}
			</fieldset>
		);
	},
	initialState: {
		optionsExpanded: false
	},
	properties: {
		field: {
			default: {}
		},
		errors: {
			default: {}
		},
		allTables: {
			default: `@tableWizard/${TABLES_DATA_PROPERTY}`
		}
	},
	actionHandlers: {
		'NOW_INPUT#INPUT': {
			effect: ({ action: { payload }, properties: { field }, dispatch }) => {
				const { fieldValue, name } = payload;
				// now-input doesn't trigger VALUE_SET if input is set to invalid which causes DEF0191362 & DEF0189518
				// send inputted when some value is entered in field so that parent can rest the error message and make field valid
				// the value_set and invalid_set should take care of rest of validations properly
				if ((name === 'label' || name === 'name') && fieldValue.length) {
					dispatch(FIELD_INPUTTED, { field, attr: name });
				}
			},
			stopPropagation: true
		},

		/**
		 * Using this new pattern of passing in a callback to `dispatch`/`updateState`/etc. that
		 * returns a declarative update. This function has access to `coeffects` and using
		 * `properties` or `state` off of this will help guarantee "freshness".
		 */
		'NOW_INPUT#VALUE_SET': ({
			action: { payload, stopPropagation },
			dispatch
		}) => {
			/**
			 * This is emitted on blur of the now-input only if the native HTML validations pass.
			 *
			 * The handler of FIELD_CHANGED will determine if there are any duplicates between
			 * the stored field values and the incoming change. To follow the Design System
			 * pattern, the new field value will not be stored on the field list component's
			 * state.
			 */
			stopPropagation();
			const { value, name } = payload;

			// For whatever reason leaving `invalidExp` as a global on the file does weird things
			const invalidExp = new RegExp('[^a-z0-9_]|_$|[_]{2,}', 'g');
			switch (name) {
				case 'label':
					dispatch(({ properties: { field } }) => {
						return {
							type: FIELD_CHANGED,
							payload: {
								field: {
									...field,
									label: value,
									name: serializeSNName(value)
								},
								attr: 'label'
							}
						};
					});
					break;
				case 'name':
					if (invalidExp.exec(value)) {
						// Don't update the field value if there are invalid characters (see below)
						return;
					}
					dispatch(({ properties: { field } }) => {
						return {
							type: FIELD_CHANGED,
							payload: {
								field: {
									...field,
									name: serializeSNName(value)
								},
								attr: 'name'
							}
						};
					});
					break;
				default:
					dispatch(({ properties: { field } }) => {
						return {
							type: FIELD_CHANGED,
							payload: {
								field: {
									...field,
									[name]: value
								},
								attr: name
							}
						};
					});
					break;
			}
		},
		'NOW_INPUT#INVALID_SET': ({
			action: {
				payload: { type, value, fieldValue, name },
				stopPropagation
			},
			dispatch
		}) => {
			/**
			 * This is emitted on blur of the now-input, `value` is a boolean of whether or not
			 * the native HTML input element is invalid or not. When `value` is `true`, the
			 * VALUE_SET action does not fire.
			 *
			 * So holding to that pattern throughout. If there's an "error", we won't actually
			 * update the fields stored on the field list component's local state.
			 */

			stopPropagation();

			let message = null;

			// For whatever reason leaving `invalidExp` as a global on the file does weird things
			// Only alphanumeric or underscores, no duplicate underscores or trailing underscores
			const invalidExp = new RegExp('[^a-z0-9_]|_$|[_]{2,}', 'g');
			// Preventing "only whitespaces", needs to be anchored to mimic behavior of HTML5 `pattern`
			const noSpaceExp = new RegExp(`^${PREVENT_ONLY_WHITESPACE_REGEX}$`);
			if (type === 'text') {
				if (value === true && (!fieldValue || !noSpaceExp.exec(fieldValue))) {
					// This was such a sneaky sneaky bug. Under stress, INVALID_SET will unreliably
					// fire even if there totally is a valid (i.e. required) fieldValue.
					message = REQUIRED_ERR_MESSAGE;
				}
				if (name === 'name' && invalidExp.exec(fieldValue)) {
					// Since manage-invalid and pattern are incompatible, we're essentially faking
					// an INVALID_SET here
					message = CHAR_INVALID_ERR_MESSAGE;
				}
			} else if (type === 'number' && value === true) {
				if (!fieldValue) {
					message = REQUIRED_ERR_MESSAGE;
				} else if (fieldValue < 1) {
					message = CHAR_LIMIT_MIN_ERR_MESSAGE;
				} else if (fieldValue > 8000) {
					message = CHAR_LIMIT_MAX_ERR_MESSAGE;
				}
			}

			dispatch(({ properties: { field } }) => {
				return {
					type: INVALID_SET,
					payload: {
						field,
						attr: name,
						message
					}
				};
			});
		},
		'NOW_CHECKBOX#CHECKED_SET': ({
			action: {
				payload: { name, value },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();
			dispatch(({ properties: { field } }) => {
				return {
					type: FIELD_CHANGED,
					payload: {
						field: {
							...field,
							[name]: value
						},
						attr: name
					}
				};
			});
		},
		'NOW_SELECT#SELECTED_ITEM_SET': ({
			properties: { field },
			action: {
				payload: { name, value },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();
			switch (name) {
				case 'type':
					return dispatch(TYPE_UPDATED, {
						field: { ...field, type: value }
					});
				case 'choice':
					return dispatch(NONE_CHOICE_UPDATED, {
						field: { ...field, choice: value }
					});
				default:
					return;
			}
		},
		'NOW_TYPEAHEAD#SELECTED_ITEM_SET': ({
			properties: { field },
			action: {
				payload: { name, value },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();
			if (name === 'reference') {
				dispatch(REFERENCE_UPDATED, {
					field: { ...field, reference: value }
				});
			}
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action: { stopPropagation } }) => {
			stopPropagation(); // Don't let this action escape
		},
		[CHOICE_ADDED]: ({
			properties: { field },
			action: { payload, stopPropagation },
			dispatch
		}) => {
			stopPropagation();
			const { newChoice } = payload;
			const id = newChoice + field.choices.length;
			const currentChoice = {
				id,
				label: newChoice,
				value: serializeSNName(newChoice)
			};
			const choices = [...field.choices, currentChoice];
			dispatch(CHOICES_UPDATED, {
				field: { ...field, choices }
			});
		},
		[CHOICE_DELETED]: ({
			properties: { field },
			action: { payload, stopPropagation },
			dispatch
		}) => {
			stopPropagation();
			const { id } = payload;
			const filteredChoices = field.choices.filter(choice => choice.id != id);

			dispatch(CHOICES_UPDATED, {
				field: { ...field, choices: filteredChoices }
			});
		},
		[CHOICE_EDITED]: ({
			properties: { field },
			action: {
				payload: { id, value, name },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();
			const newChoices = [...field.choices];
			const editedIndex = field.choices.findIndex(choice => choice.id === id);

			newChoices[editedIndex] = {
				...newChoices[editedIndex],
				// format value correctly if its the name of value
				// since thats the system value
				[name]: name === 'value' ? serializeSNName(value) : value
			};

			dispatch(CHOICES_UPDATED, {
				field: { ...field, choices: newChoices }
			});
		}
	},
	styles
});
