import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@devsnc/guided-wizard-toolkit';
import '@servicenow/now-text-link';
import 'sn-app-object-wizard-components'

import createScratch from '../../../assets/create-scratch.svg';
import existingTable from '../../../assets/existing-table.svg';
import uploadSpreadsheet from '../../../assets/upload-spreadsheet.svg';

import styles from './sn-table-wizard-source-page.scss';
import { generateStyleToolTipContent } from '../../../helpers/sn-naming-helpers';

createCustomElement('sn-table-wizard-source-page', {
	view: (
		{
			source,
			properties: {
				wizardContext: { defaultPageWidth }
			}
		},
		{ dispatch }
	) => {
		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						source: source
					});
				}}
				primaryDisabled={!source}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				maxWidth={defaultPageWidth}>
				<div className="custom-header" slot="SN_GUIDED_WIZARD_PAGE#HEADER_SLOT">
					<h2>{t('ADD DATA')}</h2>
					<h1>{t('How do you want to create a table?')}</h1>
					<span>
						{t(
							"Tables contain the data that's available in an app. If you aren't sure where to start, you can create a table from scratch and we'll help you create a new table."
						)}{' '}
						<now-text-link
							slot="SN_GUIDED_WIZARD_PAGE#FOOTER_SLOT"
							className="text-link"
							label={t('Learn more about tables.')}
							href="https://docs.servicenow.com/bundle/paris-platform-administration/page/administer/table-administration/task/t_CreateATable.html"
							opensWindow
						/>
					</span>
				</div>
				<div className="body-container">
					<sn-aow-tile-button
						className="spreadsheet"
						id="spreadsheet"
						icon={uploadSpreadsheet}
						text={t('Upload a spreadsheet')}
						isSelected={source === 'spreadsheet'}
						helperContent={generateStyleToolTipContent(
							t('Upload an XLSX file')
						)}
					/>
					<sn-aow-tile-button
						className="extend"
						id="extend"
						icon={existingTable}
						text={t('Create from an existing table')}
						isSelected={source === 'extend'}
						helperContent={generateStyleToolTipContent(
							t(
								'Select this option to create a table that shares columns from an existing table.'
							)
						)}
					/>
					<sn-aow-tile-button
						className="scratch"
						id="scratch"
						icon={createScratch}
						text={t('Create from scratch')}
						isSelected={source === 'scratch'}
						helperContent={generateStyleToolTipContent(
							t('Create column headers for a blank table')
						)}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		source: ''
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-table-wizard-source-page'
			});
		},
		['AOW_TILE_BUTTON#CLICKED']: ({
			action: {
				stopPropagation,
				payload: { id }
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ source: id });
		}
	},
	styles
});
