import {
	REQUIRED_ERR_MESSAGE,
	DUPLICATE_ERR_MESSAGE,
	CHAR_INVALID_ERR_MESSAGE,
	ONE_CHOICE_REQUIRED_ERR_MESSAGE,
	SYSTEM_VALUE_REQUIRED_ERR_MESSAGE
} from '../components/sn-table-field-list/constants';

const isLabelOrName = attr => attr === 'label' || attr === 'name';

const isAttrInvalid = (errors, attr) => {
	return !!errors[attr] && errors[attr].isDirty && !!errors[attr].message;
};

// Conditionally updates the errorState's message - to prevent the duplication error from
// overwriting a REQUIRED_ERR_MESSAGE or CHAR_INVALID_ERR_MESSAGE that is controlled by the
// field card
export const conditionalUpdateAttrErr = (fieldErrorState, message) => {
	if (
		fieldErrorState.message !== REQUIRED_ERR_MESSAGE &&
		fieldErrorState.message !== CHAR_INVALID_ERR_MESSAGE
	) {
		return {
			...fieldErrorState,
			message
		};
	}
	return fieldErrorState;
};

const addDuplicationErr = fieldErrorState =>
	conditionalUpdateAttrErr(fieldErrorState, DUPLICATE_ERR_MESSAGE);
const clearDuplicationErr = fieldErrorState =>
	conditionalUpdateAttrErr(fieldErrorState, null);

/**
 * This iterates through the `fields` array once to generate all duplication error states for
 * all fields. This is very expensive, but this code is called only if a field's `label` or
 * `name` key has been updated to a valid value.
 */
const generateDuplicationErrors = (
	field = {},
	fields,
	errors,
	extendedFields = []
) => {
	// Initialize empty objects to temporarily store field names/labels
	const labels = {};
	const names = {};
	let newErrors = {};

	// Iterate through every field...
	fields.forEach(f => {
		// Inherit the existing field error state
		newErrors[f.id] = { ...errors[f.id] };

		// If it's the field that was just changed, early exit (see below)
		if (f.id === field.id) {
			return;
		}

		// Just checking to see if there's a duplicate label and name by creating a labels object with field2 with duplicates and then field1 with duplicates

		// Checking the current iteration's `label` value...
		if (labels[f.label]) {
			// If it already exists in the `label` object, that means that it shares a `label`
			// with a previous iteration. Add a duplication error for both fields
			newErrors[f.id].label = addDuplicationErr({ ...newErrors[f.id].label });
			newErrors[labels[f.label]].label = addDuplicationErr({
				...newErrors[labels[f.label]].label
			});
		} else {
			// If it doesn't exist in the `label` object, that means that this is a unique `label`
			// value. First, store `label` value along with the current iteration's id. Then,
			// make sure the current iteration's duplication error is cleared.
			labels[f.label] = f.id;
			newErrors[f.id].label = clearDuplicationErr({ ...newErrors[f.id].label });
		}

		// same with name instead of label here

		// Checking the current iteration's `name` value...
		if (names[f.name]) {
			newErrors[f.id].name = addDuplicationErr({ ...newErrors[f.id].name });
			newErrors[names[f.name]].name = addDuplicationErr({
				...newErrors[names[f.name]].name
			});
		} else {
			names[f.name] = f.id;
			newErrors[f.id].name = clearDuplicationErr({ ...newErrors[f.id].name });
		}

		// These iterations also handle duplicate values that appear in more than 2 fields.
	});

	// Exit early if this is a FIELD_DELETED case
	if (!field.id) {
		return newErrors;
	}

	// finally checks the new field you just updated

	/**
	 * Instead of updating the changed field non-deterministically through the above iteration,
	 * always update it last to ensure expected behavior.
	 */
	if (labels[field.label]) {
		newErrors[field.id].label = addDuplicationErr({
			...newErrors[field.id].label
		});
		newErrors[labels[field.label]].label = addDuplicationErr({
			...newErrors[labels[field.label]].label
		});
	} else {
		newErrors[field.id].label = clearDuplicationErr({
			...newErrors[field.id].label
		});
		labels[field.label] = field.id;
	}

	if (names[field.name]) {
		newErrors[field.id].name = addDuplicationErr({
			...newErrors[field.id].name
		});
		newErrors[names[field.name]].name = addDuplicationErr({
			...newErrors[names[field.name]].name
		});
	} else {
		newErrors[field.id].name = clearDuplicationErr({
			...newErrors[field.id].name
		});
		names[field.name] = field.id;
	}

	// For supporting existing fields from an extended table, why last? So there's no opportunity
	// to write to errors for extended fields
	extendedFields.forEach(f => {
		// Checking the current iteration's `label` value...
		if (labels[f.label]) {
			// If it already exists in the `label` object, that means that it shares a `label`
			// with a previous iteration but only add a duplication error for new field
			newErrors[labels[f.label]].label = addDuplicationErr({
				...newErrors[labels[f.label]].label
			});
		}

		// Checking the current iteration's `name` value...
		if (names[f.name]) {
			newErrors[names[f.name]].name = addDuplicationErr({
				...newErrors[names[f.name]].name
			});
		}
	});

	return newErrors;
};

/**
 * The now-dropdown doesn't have an INVALID_SET action, so we have to set the required error
 * like so instead of inside of the field card. The now-dropdown is used here temporarily
 * until the now-select is available, which does dispatch INVALID_SET.
 */
const validType = {
	type: {
		isDirty: true,
		message: null
	}
};

/**
 * The field `type` attribute changing has affects on both the expected structure of the field
 * entity passed to the backend and the validations/errors that need to be shown to the user
 */
const updateFieldType = (field, fields, errors) => {
	let newField = { ...field };
	let newErrors = { ...errors };
	/**
	 * There are three field types that require additional keys and validations:
	 * 1. string - requires `length` key, that defaults to 80, has to be a number, and
	 *      needs be between 1-8000
	 * 2. reference - requires `reference` key
	 * 3. choice - requires a whole bunch of other stuff, out of scope for now.
	 */
	//

	switch (field.type) {
		/* eslint-disable no-unused-vars */
		case 'string': {
			const {
				reference,
				choice,
				choices,
				newChoice,
				...restOfField
			} = newField; // Ensure other potential optional keys are gonezo
			newField = { ...restOfField, length: '40' }; // Default value for `length`

			const {
				reference: referenceErrKey,
				choice: choiceTypeErrKey,
				...restOfErrors
			} = newErrors[field.id]; // Similar to the field, ensure other potential attribute error keys are cleared
			newErrors[field.id] = {
				...restOfErrors,
				...validType,
				length: {
					isDirty: false,
					message: null // Defaults to `null` as there's a default value for `length`
				}
			};
			break;
		}

		case 'reference': {
			const {
				length,
				choiceType,
				choice,
				choices,
				newChoice,
				...restOfField
			} = newField; // Ensure other potential optional keys are gonezo
			newField = { ...restOfField, reference: null }; // No default value for `reference`

			const {
				length: lengthErrKey,
				choice: choiceTypeErrKey,
				...restOfErrors
			} = newErrors[field.id];
			newErrors[field.id] = {
				...restOfErrors,
				...validType,
				reference: {
					isDirty: false,
					message: REQUIRED_ERR_MESSAGE
				}
			};
			break;
		}

		case 'choice': {
			const { length, reference, choice = '1', ...restOfField } = newField; // Ensure other potential optional keys are gonezo
			newField = { ...restOfField, choice, choices: [] };

			const {
				length: lengthErrKey,
				reference: referenceErrKey,
				...restOfErrors
			} = newErrors[field.id];
			newErrors[field.id] = {
				...restOfErrors,
				...validType,
				choice: {
					isDirty: false,
					message: null
				}
			};
			break;
		}

		default: {
			const {
				length,
				reference,
				choice,
				choices,
				newChoice,
				...restOfField
			} = newField;
			newField = { ...restOfField }; // Clear out all the optional keys from the field

			const {
				length: lengthErrKey,
				reference: referenceErrKey,
				choice: choiceTypeErrKey,
				...restOfErrors
			} = newErrors[field.id];
			newErrors[field.id] = { ...restOfErrors, ...validType }; // Clear out all the optional keys from the field's error state
			break;
		}
		/* eslint-enable no-unused-vars */
	}

	const newFields = fields.map(f => (f.id === field.id ? { ...newField } : f));

	return { newFields, newErrors };
};

// Update the reference of the field
const updateReference = (field, fields, errors) => {
	const newErrors = { ...errors };

	newErrors[field.id] = {
		...errors[field.id],
		reference: { isDirty: true, message: null }
	};

	const newFields = fields.map(f => (f.id === field.id ? field : f));

	return { newFields, newErrors };
};

// Update the choice of the field
const updateChoice = ({ field, fields, errors }) => {
	const newErrors = { ...errors };
	const choiceErrMessage =
		field.choice === '3' && field.choices.length === 0
			? ONE_CHOICE_REQUIRED_ERR_MESSAGE
			: null;

	const inValidChoices = field.choices.some(choice => choice.value === '');

	newErrors[field.id] = {
		...errors[field.id],
		choice: {
			isDirty: true,
			message: choiceErrMessage
		},
		choices: {
			...(inValidChoices
				? {
						isDirty: true,
						message: SYSTEM_VALUE_REQUIRED_ERR_MESSAGE
				  }
				: {
						isDirty: true,
						message: null
				  })
		}
	};

	const newFields = fields.map(f => (f.id === field.id ? field : f));

	return { newFields, newErrors };
};

// Determine whether all fields are valid or not
const validateFields = errors => {
	// Iterate through every field's error state
	for (const fieldId in errors) {
		// Return the error state for each attribute of the field
		const attrs = Object.values(errors[fieldId]);

		// Check if any of the attributes has an error message
		const hasError = attrs.some(attr => {
			if (!attr) {
				return false;
			}
			return typeof attr.message === 'string';
		});

		// Return `false` immediately if one is found
		if (hasError) {
			return false;
		}
	}

	// If none of the attributes on any field have error messages, return `true`
	return true;
};

// Generate a valid autonumbering prefix based on table label
const getAutonumberPrefix = label => {
	return label
		.replace(/[^a-zA-Z]/g, '')
		.toUpperCase()
		.slice(0, 3);
};

export default {
	isLabelOrName,
	isAttrInvalid,
	generateDuplicationErrors,
	updateFieldType,
	updateReference,
	updateChoice,
	validateFields,
	getAutonumberPrefix
};
