import fetchHelper, { failedResponse } from '../../../helpers/fetch-helper';
import { waitFor } from '../../../helpers/wait-for-util';
import { IMPORT_STATE_RUNNING, IMPORT_STATE_ERROR } from './constants';
import { t } from 'sn-translate';

const importFromImportSetAPI = async (appId, tableName, body) =>
	await fetchHelper(
		`/api/now/appcreator/tables/${tableName}/import?sysparm_transaction_scope=${appId}`,
		{
			method: 'POST',
			body: JSON.stringify(body)
		}
	);

const tableRecordAPI = async name =>
	await fetchHelper(
		`/api/now/table/sys_db_object?sysparm_query=name=${name}&sysparm_fields=sys_id`
	);

export const getTableSysId = async name => {
	const response = await tableRecordAPI(name);

	if (response.isError) {
		return response;
	}

	const { sys_id: sysId } = response.result[0];

	return {
		isError: false,
		sysId
	};
};

const deleteRecordAPI = async (table, recordId, appId) =>
	await fetchHelper(
		`/api/now/table/${table}/${recordId}?sysparm_transaction_scope=${appId}`,
		{
			method: 'DELETE'
		}
	);

const sessionNotificationAPI = async appId =>
	await fetchHelper(
		`/api/now/session/notification?sysparm_transaction_scope=${appId}`
	);

export const importDataFromImportSet = async (
	appId,
	tableInfo,
	importSetTableName
) => {
	// Run import set
	const data = buildReqData(tableInfo, importSetTableName);
	const response = await importFromImportSetAPI(appId, tableInfo.name, data);

	if (response.isError) {
		return response;
	}

	// Wait for import to complete
	const importSetSysId = response.result;
	const importResponse = await waitForSuccessfulImport(importSetSysId, appId);

	if (importResponse.isError) {
		return importResponse;
	}

	const importSetTableIdResponse = await getTableSysId(
		importSetTableName,
		appId
	);

	if (importSetTableIdResponse.isError) {
		return importSetTableIdResponse;
	}
	await deleteRecordAPI('sys_db_object', importSetTableIdResponse.sysId, appId);

	// Delete record api creates session notification. calling session notification api will clear it and doesn't pollute other places
	await sessionNotificationAPI(appId);

	return importResponse;
};

// Polls the progress of the data import. Returns the results of the import, when it
// completes.  Otherwise, throws an error indicating the import failed.
async function waitForSuccessfulImport(importSetSysId, appId) {
	const response = await waitFor(
		`api/now/table/sys_import_set_run?sysparm_query=set%3D${importSetSysId}&sysparm_transaction_scope=${appId}`,
		async response => {
			if (response.isError) {
				return true;
			}
			const result = response.result[0];
			return result && result.state != IMPORT_STATE_RUNNING;
		}
	);

	if (response.isError || response.result[0].state === IMPORT_STATE_ERROR) {
		return failedResponse(t('Import error'));
	}
	return response.result[0];
}

export function canRunImportSets(id, sheets) {
	const notEligible = { canImport: false };
	if (id && sheets) {
		const match = sheets.find(item => item.name === id);

		if (match) {
			const { importDataCheckbox = false, importSetTableName } = match;
			if (importDataCheckbox) {
				return { canImport: true, importSetTableName };
			}
		}
	}
	return notEligible;
}

function buildReqData(tableInfo, importSetTableName) {
	return {
		name: `${tableInfo.name}_transform_map`,
		import_set_table_name: importSetTableName,
		transform_entries: tableInfo.fields
			.filter(f => f.initialName)
			.map(f => ({
				source_field: f.initialName,
				target_field: f.name
			}))
	};
}
