import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	BEHAVIOR_NAME,
	CHECK_UNIQUE_FIELD,
	CHECK_UNIQUE_FIELD_REQUEST,
	CHECK_UNIQUE_FIELD_REQUEST_STARTED,
	CHECK_UNIQUE_FIELD_STARTED,
	CHECK_UNIQUE_FIELD_SUCCESS,
	CHECK_UNIQUE_FIELD_FAILURE,
	CHECK_UNIQUE_FIELD_COMPLETED,
	DEFAULT_QUERY_PARAMS
} from './constants';

const requestFetchEffect = createHttpEffect('', {
	method: 'GET',
	startActionType: CHECK_UNIQUE_FIELD_REQUEST_STARTED,
	successActionType: CHECK_UNIQUE_FIELD_SUCCESS,
	errorActionType: CHECK_UNIQUE_FIELD_FAILURE,
	batch: true
});

export default {
	name: BEHAVIOR_NAME,

	actionHandlers: {
		[CHECK_UNIQUE_FIELD]: ({ action, dispatch }) => {
			const {
				url = null,
				fieldName,
				tableName,
				value,
				queryParams = null,
				sysparm_transaction_scope
			} = action.payload;

			if (
				typeof sysparm_transaction_scope !== 'string' ||
				sysparm_transaction_scope.length < 1
			) {
				throw new Error(
					`[${BEHAVIOR_NAME}] '${CHECK_UNIQUE_FIELD}' requires that you pass the current app 'sysparm_transaction_scope' in the payload`
				);
			}

			/** Setup queryParams */
			// If queryParams are supplied, use them
			// If they aren't, create a sysparm_query to look up the value of the fieldName
			let restParams = {
				...DEFAULT_QUERY_PARAMS,
				sysparm_transaction_scope
			};

			if (!queryParams) {
				const sysparm_query = `${fieldName}=${value}`;
				restParams = {
					...restParams,
					sysparm_query
				};
			} else {
				restParams = {
					...restParams,
					...queryParams
				};
			}

			/** Setup url */
			// If no url is provided, default to the table api with given tableName
			const requestUrl = url ? url : `/api/now/table/${tableName}`;
			dispatch(CHECK_UNIQUE_FIELD_REQUEST, {
				url: requestUrl,
				tableName,
				restParams: Object.keys(restParams),
				...restParams
			});
		},
		// TODO: Lets refactor or update the api call so that it returns
		// the fieldName and value as well
		[CHECK_UNIQUE_FIELD_REQUEST]: {
			...requestFetchEffect,
			// Jumping through hoops to allow dynamic urls here
			interceptors: [
				{
					after: context => {
						const url = context.coeffects.action.payload.url;
						context.effects[0].args[0] = url;

						const restParams = context.coeffects.action.payload.restParams;
						context.effects[0].args[1].queryParams = restParams;
						return context;
					}
				}
			]
		},
		[CHECK_UNIQUE_FIELD_REQUEST_STARTED]: ({ state, dispatch }) => {
			const { value, fieldName, tableName } = state;
			dispatch(CHECK_UNIQUE_FIELD_STARTED, {
				value,
				fieldName,
				tableName
			});
		},
		[CHECK_UNIQUE_FIELD_SUCCESS]: ({ dispatch, action }) => {
			const { result } = action.payload;

			dispatch(CHECK_UNIQUE_FIELD_COMPLETED, {
				result,
				unique: result && result.length ? false : true
			});
		},
		[CHECK_UNIQUE_FIELD_FAILURE]: ({ action: { payload } }) =>
			console.warn(
				`${CHECK_UNIQUE_FIELD_REQUEST} failed with error: `,
				payload.error
			)
	}
};
