import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-collapse';
import { generateStyleToolTipContent} from 'sn-app-object-wizard-components';

import { SPREADSHEET_INFO_CARD__CONVERT_TO_TABLE } from './constants';
import { VALIDATE } from '../../../../constants';
import { NO_DOTS_REGEX_STRING } from '../../../../helpers/error-constants';
import {
	generateStartingNumberError,
	validateDetails
} from '../../../../helpers/spreadsheet-row-validation';
import fieldValidationHelpers from '../../../../helpers/table-field-validations';

import styles from './sn-tb-spreadsheet-info-card.scss';

const { isAttrInvalid } = fieldValidationHelpers;

createCustomElement('sn-tb-spreadsheet-info-card', {
	view: (
		{ headerNumber, importDataCheckbox, properties, errors, isFormValid },
		{ updateProperties }
	) => {
		const { sheet, fileName, expanded, completed } = properties;

		const { name, table = {} } = sheet;

		let data = [];

		if (completed) {
			data = [
				{ label: t('Spreadsheet'), text: fileName },
				{ label: t('Sheet name'), text: name },
				{ label: t('Table name'), text: table.label },
				{ label: t('uppercase_fields'), text: (table.fields && table.fields.length) || 0 }
			];
			return <sn-aow-info-summary data={data} />;
		}

		data = [
			{ label: t('Spreadsheet'), text: fileName, maxCharLength: 9 },
			{ label: t('Sheet name'), text: name, maxCharLength: 9 },
			{ custom: true, label: 'collapse' }
		];

		return (
			<sn-aow-info-summary data={data}>
				<div
					slot="AOW_INFO_SUMMARY#CUSTOM_CONTENT_collapse"
					className="collapse-trigger-container">
					<now-button
						className="convert-table-button"
						variant="secondary"
						label={t('Convert to table')}
						disabled={!isFormValid}
					/>
					<now-button-iconic
						role="button"
						on-click={() => updateProperties({ expanded: !expanded })}
						icon={`chevron-${expanded ? 'up' : 'down'}-fill`}
						config-aria={{
							'aria-label': t('More options'),
							'aria-expanded': expanded ? 'true' : 'false'
						}}
						bare
					/>
				</div>
				<now-collapse
					expanded={expanded}
					slot="AOW_INFO_SUMMARY#EXTENDED_CONTENT">
					<div className="sn-table-spreadsheet-inputs">
						<now-input
							className="header-number-input"
							name="headerNumber"
							label={t('Enter a row number for the table header')}
							size="lg"
							autocomplete="off"
							type="number"
							min={1}
							step={1}
							required
							helperContent={generateStyleToolTipContent(
								t(
									'Enter the row number in which the column headers are located. For example, if the column headers are in the second row of your spreadsheet, you would enter 2.'
								)
							)}
							value={headerNumber}
							pattern={NO_DOTS_REGEX_STRING}
							messages={
								isAttrInvalid(errors, 'headerNumber')
									? [
											{
												status: 'critical',
												content: errors.headerNumber.message
											}
									  ]
									: []
							}
						/>
						<sn-aow-checkbox-tooltip
							helperContent={generateStyleToolTipContent(
								t(
									'To include the data that is after the column headers in your spreadsheet, select this option'
								)
							)}>
							<now-checkbox
								slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
								name="importDataCheckbox"
								label={t('Import spreadsheet data')}
								checked={importDataCheckbox}
							/>
						</sn-aow-checkbox-tooltip>
					</div>
				</now-collapse>
			</sn-aow-info-summary>
		);
	},
	initialState: {
		headerNumber: 1,
		importDataCheckbox: false,
		errors: {
			headerNumber: {
				message: null
			}
		},
		isFormValid: true
	},
	properties: {
		expanded: { default: false },
		fileName: { default: '' },
		completed: { default: false },
		sheet: {
			default: {
				name: '',
				table: {}
			}
		}
	},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': ({
			state: { headerNumber, importDataCheckbox },
			properties: { sheet },
			dispatch,
			action: { stopPropagation }
		}) => {
			stopPropagation();
			dispatch(SPREADSHEET_INFO_CARD__CONVERT_TO_TABLE, {
				sheet,
				headerNumber,
				importDataCheckbox
			});
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_INPUT#INVALID_SET']: ({
			action: { payload, stopPropagation },
			updateState,
			dispatch
		}) => {
			stopPropagation();
			const { name, fieldValue } = payload;
			let message = null;

			if (name === 'headerNumber') {
				message = generateStartingNumberError(fieldValue);
			}

			// INVALID_SET is fired on every input blur, can reliably capture `isDirty` state
			updateState({
				path: `errors.${name}`,
				value: { isDirty: true, message },
				operation: 'set'
			});

			dispatch(VALIDATE, { message });
		},
		[VALIDATE]: ({ updateState }) => {
			updateState(({ state }) => {
				const isFormValid = validateDetails(state);
				return { isFormValid };
			});
		},
		['NOW_CHECKBOX#CHECKED_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		}
	},
	styles
});
