import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import '@devsnc/guided-wizard-toolkit';
import 'sn-app-object-wizard-components';

import { t } from 'sn-translate';

import styles from './styles.scss';

createCustomElement('sn-table-wizard-summary-page', {
	view: (
		{
			properties: {
				wizardContext: { table }
			}
		},
		{ dispatch }
	) => {
		const onComplete = () =>
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'sys_db_object',
				result: table
			});
		return (
			<sn-guided-wizard-page
				className="sn-table-wizard-summary-page"
				onPrimaryClick={onComplete}
				primaryLabel={t('Done')}
				secondaryLabel={t('Edit table')}
				onSecondaryClick={() =>
					dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
						key: 'sys_db_object',
						result: table
					})
				}
				onClose={onComplete}
				running-header-text={t('CREATE TABLE')}
				headerText={t('Your table is ready')}
				subheaderText={t(
					"We'll take you to the app home where you can add more data, experience, logic and security to your app."
				)}>
				<sn-aow-info-summary
					data={[
						{
							label: t('Source'),
							text: table.approach
						},
						{
							label: t('Table name'),
							text: table.label
						},
						{
							// Making use of existing sys_ui_message record for translation ( key : uppercase_fields, message: Fields )to resolve DEF0191366
							label: t('uppercase_fields'),
							text: table.fields ? table.fields.length : 0
						}
					]}
				/>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');
		}
	},
	styles
});
