import { CHECK_UNIQUE_FIELD } from './constants';

export default function checkUniqueField(
	dispatch,
	options,
	sysparm_transaction_scope
) {
	const { url, fieldName, tableName, value, ...rest } = options;
	dispatch(CHECK_UNIQUE_FIELD, {
		url,
		fieldName,
		tableName,
		value,
		sysparm_transaction_scope,
		...rest
	});
}
