import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@servicenow/now-button';
import '@servicenow/now-input';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

import { CHOICE_DELETED, CHOICE_EDITED } from '../sn-table-choices/constants';

import styles from './sn-table-choice-list.scss';

createCustomElement('sn-table-choice-list', {
	view: ({ properties: { choices } }, { dispatch }) => {
		return (
			<div className="sn-table-choice-list-container">
				<div className="choice-list-column-headers">
					<ul>
						<li>
							<p>{t('Display value')}</p>
							<sn-aow-helper-content
								helperContent={generateStyleToolTipContent(
									t(
										'This is the value users will see in the dropdown when working in a form.'
									)
								)}
							/>
						</li>
						<li>
							<p>{t('System value')}</p>
							<sn-aow-helper-content
								helperContent={generateStyleToolTipContent(
									t('The is the value that will be stored in the table.')
								)}
							/>
						</li>
					</ul>
				</div>
				{choices.map(({ label, value, id }, index) => {
					const rowNum = index + 1;
					return (
						<div className="table-choice-list">
							<p className="list-number">{rowNum}</p>
							<now-input
								name="label"
								value={label}
								append-to-payload={{ id }}
								config-aria={{
									'aria-label': t('Display value')
								}}
							/>
							<now-input
								name="value"
								value={value}
								append-to-payload={{ id }}
								config-aria={{
									'aria-label': t('System value')
								}}
							/>
							<p className="list-icon">
								<now-button-iconic
									class="delete-choice-button"
									bare
									icon="trash-fill"
									on-click={() => dispatch(CHOICE_DELETED, { id })}
									variant="secondary"
									config-aria={{
										'aria-label': t('Delete choice')
									}}
									hide-padding
								/>
							</p>
						</div>
					);
				})}
			</div>
		);
	},
	properties: {
		choices: { default: [] }
	},
	actionHandlers: {
		['NOW_INPUT#VALUE_SET']: ({
			action: {
				payload: { value, id, name },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();

			dispatch(CHOICE_EDITED, {
				name,
				value,
				id
			});
		}
	},
	styles
});
