import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-button';

import { CHOICE_ADDED } from '../sn-table-choices/constants';

import styles from './sn-table-choice-input.scss';

createCustomElement('sn-table-choice-input', {
	view: ({ newChoice }, { dispatch }) => {
		return (
			<div className="sn-table-choice-input-container">
				<now-input name="newChoice" value={newChoice} label={t('Choices')} />
				<now-button
					on-click={() => dispatch(CHOICE_ADDED, { newChoice })}
					disabled={!newChoice}
					label={t('Add')}
					variant="primary"
				/>
			</div>
		);
	},
	initialState: {
		newChoice: ''
	},
	properties: {
		noneChoice: { default: '1' },
		choicesLength: { default: 0 }
	},
	actionHandlers: {
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({
				[name]: value
			});
		},
		[CHOICE_ADDED]: ({ updateState }) => {
			updateState({
				newChoice: ''
			});
		}
	},
	styles
});
