import { t } from 'sn-translate';

// All error messages/regex strings needed for table wizard validations

// Generic
export const REQUIRED_ERR = t('Required');
export const INVALID_CHAR_ERR = t('Invalid characters');
// Regex matches valid system name formats (alphanumeric & _ only, all lowercase, no repeating _ or trailing _)
export const VALID_CHAR_REGEX_STRING = '^(?!.*([_])\\1{1,})[a-z0-9_]+[^_]$';
export const PREVENT_ONLY_WHITESPACE_REGEX = '(?!^ +$).*';

// Specific to field definition page
export const DUPLICATE_FIELD_ERR = t('Must be unique');
export const MIN_CHAR_LIMIT_ERR = t('Minimum is 0');
export const MIN_CHAR_LIMIT_ONE_ERR = t('Minimum is 1');
export const MAX_CHAR_LIMIT_ERR = t('Maximum is 8000');
export const ONE_CHOICE_REQUIRED_ERR = t(
	'At least one choice option is required'
);

// Specific table details page
export const INVALID_TABLE_NAME_ERR = t('Invalid name');
export const NO_LOWERCASE_ERR = t('Must be uppercase');
export const MIN_STARTING_NUMBER_ERR = t('Minimum is 0');
export const MIN_AUTO_DIGITS_ERR = t('Minimum is 1');
export const MAX_AUTO_DIGITS_ERR = t('Maximum is 15');
export const NO_PUNCTUATION_ERR = t('No punctuation');
export const VALID_AUTO_PREFIX_REGEX_STRING = '^[A-Z]+$';
export const HAS_LOWERCASE_REGEX_STRING = '[a-z]';
export const NO_DOTS_REGEX_STRING = '^[^.]+$';
