import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import {
	VALIDATE,
	CREATE_TABLE,
	GENERATE_AUTONUMBER_ERRORS,
	GENERATE_ERROR_MESSAGES,
	IMPORT_DATA_REQUESTED,
	IMPORT_DATA_IN_PROGRESS,
	IMPORT_DATA_FAILED,
	IMPORT_DATA_SUCCEEDED,
	TABLE_ID_FAILED,
	CHECK_TABLE_AUTONUMBER_PREFIX,
	CHECK_TABLENAME_UNIQUENESS,
	INITIATE_TABLE_API_CALL,
	DEFAULT_AUTONUMBER_NUMBER,
	DEFAULT_AUTONUMBER_DIGITS
} from './constants';
import {
	importDataFromImportSet,
	canRunImportSets,
	getTableSysId
} from './utils';

import '@servicenow/now-input';
import '@servicenow/now-checkbox';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import uniqueFieldBehavior from '../../../behaviors/unique-field';
import {
	getSNTableName,
	isValidSNTableName
} from '../../../helpers/sn-naming-helpers';
import detailsValidationHelpers from '../../helpers/table-details-validations';
import {
	NO_DOTS_REGEX_STRING,
	INVALID_TABLE_NAME_ERR,
	REQUIRED_ERR,
	INVALID_CHAR_ERR,
	PREVENT_ONLY_WHITESPACE_REGEX
} from '../../helpers/error-constants';

const {
	helpers: { checkUniqueField },
	actionTypes: { CHECK_UNIQUE_FIELD_COMPLETED }
} = uniqueFieldBehavior;

const {
	getAutonumberPrefix,
	generatePrefixError,
	generateStartingNumberError,
	generateDigitsError,
	validateDetails
} = detailsValidationHelpers;

const { getStateFromContext } = contextHelpers({
	namespace: 'table',
	keys: ['label', 'name', 'isExtendable', 'enableAutonumber', 'autonumber']
});

const sourceApproachMap = {
	scratch: t('From Scratch'),
	extend: t('Extended'),
	spreadsheet: t('Spreadsheet')
};

// Switching out from state to a variable for DEF0185265
// The bug was that the inputs were dispatching much faster
// than state was updating, so when it got to the second
// input (usually the prefix check)
// it was getting stale data
let currentUniqueChecks = [];

export const generateAutonumberErrors = autonumberState => {
	// Since the conditional rendering resets the state to what was previously stored,
	// also resetting `isDirty` state by default here
	const defaultErr = { isDirty: false, message: null };
	return {
		prefix: {
			...defaultErr,
			message: generatePrefixError(autonumberState.prefix)
		},
		number: {
			...defaultErr,
			message: generateStartingNumberError(autonumberState.number)
		},
		digits: {
			...defaultErr,
			message: generateDigitsError(autonumberState.digits)
		}
	};
};

// Moved this out to a separate file so that the main file is a little smaller since this wont change as much
export default {
	[actionTypes.COMPONENT_DOM_READY]: ({
		properties: { wizardContext },
		dispatch,
		updateState
	}) => {
		const initState = getStateFromContext(wizardContext);
		updateState({
			...initState,
			// this fixes an error where the values are populated
			// and then user presses previous arrow
			// and comes back to this page
			errors: {
				label: {
					isDirty: false,
					message: wizardContext.table.label ? null : REQUIRED_ERR
				},
				name: {
					isDirty: false,
					message: wizardContext.table.name ? null : REQUIRED_ERR
				},
				autonumber: {
					prefix: {
						isDirty: false,
						message:
							wizardContext.table.autonumber &&
							wizardContext.table.autonumber.prefix
								? null
								: REQUIRED_ERR
					},
					number: { isDirty: false, message: null },
					digits: { isDirty: false, message: null }
				}
			}
		});
		dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
			page: 'sn-table-wizard-details-page'
		});
		if (wizardContext.table.name) {
			dispatch(INITIATE_TABLE_API_CALL, {
				name: 'name',
				value: wizardContext.table.name,
				cb: () =>
					wizardContext.table.autonumber.prefix &&
					dispatch(CHECK_TABLE_AUTONUMBER_PREFIX, {
						value: wizardContext.table.autonumber.prefix
					})
			});
		}
	},
	['NOW_INPUT#VALUE_SET']: ({
		state: { label, errors },
		properties: {
			wizardContext: { app, user }
		},
		action: { payload },
		updateState
	}) => {
		const { value, name } = payload;
		const isMaint = user.roles.includes('maint');
		// Clear state if no value is provided, aka deleted
		switch (name) {
			case 'label': {
				// Updating label updates both name and autonumber prefix
				const tableName = getSNTableName(app.scope, value, isMaint);
				const prefix = getAutonumberPrefix(value);

				// Only update label, name and autonumber.prefix if user doesnt
				// erase out the value in label
				if (value) {
					updateState({
						label: value,
						name: tableName
					});

					// Handling this in another `updateState` call because of nesting on `autonumber`
					updateState({
						path: 'autonumber.prefix',
						value: prefix,
						operation: 'set'
					});
				} else {
					// if there's no value in label
					// get rid of the table name if there's an error
					if (errors.name.message) {
						updateState({
							label: value,
							name: tableName
						});
					} else {
						updateState({
							label: value
						});
					}

					// user erases out value of label
					// only get rid of autonumber prefix if there's no error message
					if (errors.autonumber.prefix.message) {
						updateState({
							path: 'autonumber.prefix',
							value: prefix,
							operation: 'set'
						});
					}
				}

				break;
			}
			case 'name': {
				// Don't save the name value if it doesn't start with the proper scope
				const isValidPrefix = isValidSNTableName(
					value,
					label,
					app.scope,
					isMaint
				);
				if (!isValidPrefix) {
					return;
				}

				updateState({ name: value });
				break;
			}
			case 'autonumber.number':
			case 'autonumber.digits': {
				// Don't save the value if there's a dot (to enforce whole numbers)
				const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
				if (!validRegex.exec(value)) {
					return;
				}

				updateState({
					path: name,
					value: value,
					operation: 'set'
				});

				break;
			}
			default:
				updateState({
					path: name,
					value: value,
					operation: 'set'
				});

				break;
		}
	},
	['NOW_INPUT#INVALID_SET']: ({
		state,
		properties: {
			wizardContext: { app, user }
		},
		action: {
			payload: { value, fieldValue, name },
			stopPropagation
		},
		updateState,
		dispatch
	}) => {
		stopPropagation();

		let message = null;
		const isMaint = user.roles.includes('maint');
		if (name === 'name') {
			if (value === true && fieldValue) {
				message = INVALID_CHAR_ERR;
			} else if (
				!isValidSNTableName(fieldValue, state.label, app.scope, isMaint)
			) {
				message = INVALID_TABLE_NAME_ERR;
			}
		}

		if (name === 'autonumber.prefix') {
			message = generatePrefixError(fieldValue);
		}

		if (name === 'autonumber.number') {
			message = generateStartingNumberError(fieldValue);
			if (!message) {
				const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
				if (!validRegex.exec(value)) {
					return;
				}
			}
		}

		if (name === 'autonumber.digits') {
			message = generateDigitsError(fieldValue);
			if (!message) {
				const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
				if (!validRegex.exec(value)) {
					return;
				}
			}
		}

		// Preventing "only whitespaces", needs to be anchored to mimic behavior of HTML5 `pattern`
		const noSpaceExp = new RegExp(`^${PREVENT_ONLY_WHITESPACE_REGEX}$`);

		// Fallback - if there's an error and it isn't a recognized key, assume it's required
		if (value === true && (!fieldValue || !noSpaceExp.exec(fieldValue))) {
			message = REQUIRED_ERR;
		}

		// This fixes a bug where when user updates
		// label input that is already invalid
		// it doesn't call value set.
		// Updates table name and prefix
		// Also makes sure that the user's input is different from previous input
		// (fieldValue !== state.label)
		// Since invalid_set acts as an onBlur as well
		// And only update all three fields if user doesn't erase out label input
		if (
			!message &&
			name === 'label' &&
			fieldValue &&
			fieldValue !== state.label
		) {
			// Updating label updates both name and autonumber prefix
			const tableName = getSNTableName(app.scope, fieldValue, isMaint);
			const prefix = getAutonumberPrefix(fieldValue);

			updateState({
				label: fieldValue,
				name: tableName
			});

			// Handling this in another `updateState` call because of nesting on `autonumber`
			if (state.enableAutonumber) {
				updateState({
					path: 'autonumber.prefix',
					value: prefix,
					operation: 'set'
				});
			}
		}

		// INVALID_SET is fired on every input blur, can reliably capture `isDirty` state
		updateState({
			path: `errors.${name}`,
			value: { isDirty: true, message },
			operation: 'set'
		});

		if (
			!message &&
			(name === 'name' || name === 'label' || name === 'autonumber.prefix') &&
			!value
		) {
			dispatch(INITIATE_TABLE_API_CALL, {
				name,
				value: fieldValue
			});
		} else {
			dispatch(VALIDATE);
		}
	},
	['NOW_CHECKBOX#CHECKED_SET']: ({
		state: { autonumber, errors },
		updateState,
		action,
		dispatch
	}) => {
		const {
			payload: { name, value }
		} = action;
		action.stopPropagation();
		updateState({ [name]: value });
		if (name === 'enableAutonumber' && value) {
			// We rely on the INVALID_SET to determine the validity of the properties, but
			// the input is conditionally rendered and may not be there to dispatch INVALID_SET
			// so being heavy handed to cover the edge cases here

			// if autonumber digits is empty and user has already touched it
			// set it to default of 7
			if (autonumber.digits === '' && errors.autonumber.digits.isDirty) {
				updateState({
					path: 'autonumber.digits',
					value: DEFAULT_AUTONUMBER_DIGITS,
					operation: 'set'
				});
			}

			// if autonumber number is empty and user has already touched it
			// set it to default of 1000
			if (autonumber.number === '' && errors.autonumber.number.isDirty) {
				updateState({
					path: 'autonumber.number',
					value: DEFAULT_AUTONUMBER_NUMBER,
					operation: 'set'
				});
			}

			// Generate autonumbers first
			// and then that action will dispatch
			// the unique prefix api call
			dispatch(GENERATE_AUTONUMBER_ERRORS);
		}

		// If user disables autonumber and there's an error in the autonumber prefix, number and digits
		// wipe out the value and validate form again
		if (name === 'enableAutonumber' && !value) {
			if (errors.autonumber.prefix.message) {
				updateState({
					path: 'autonumber.prefix',
					value: '',
					operation: 'set'
				});
			}
			if (errors.autonumber.number.message) {
				updateState({
					path: 'autonumber.number',
					value: '',
					operation: 'set'
				});
			}
			if (errors.autonumber.digits.message) {
				updateState({
					path: 'autonumber.digits',
					value: '',
					operation: 'set'
				});
			}

			dispatch(VALIDATE);
		}
	},
	[INITIATE_TABLE_API_CALL]: ({
		action: { payload, stopPropagation },
		dispatch
	}) => {
		stopPropagation();

		switch (payload.name) {
			case 'name':
				dispatch(CHECK_TABLENAME_UNIQUENESS, {
					value: payload.value,
					cb: payload.cb
				});
				break;
			case 'autonumber.prefix':
				dispatch(CHECK_TABLE_AUTONUMBER_PREFIX, {
					value: payload.value
				});
				break;
			default:
				break;
		}
	},
	[CHECK_TABLENAME_UNIQUENESS]: ({
		dispatch,
		action: { payload, stopPropagation },
		properties: {
			wizardContext: { app }
		}
	}) => {
		stopPropagation();

		// pushing the current api call into an array in state
		// so that I can use this info later
		currentUniqueChecks = [
			...currentUniqueChecks,
			{
				tableName: 'sys_db_object',
				fieldName: 'name',
				value: payload.value
			}
		];
		checkUniqueField(
			dispatch,
			{
				tableName: 'sys_db_object',
				fieldName: 'name',
				value: payload.value
			},
			app.sys_id
		);

		payload.cb && payload.cb();
	},
	[CHECK_TABLE_AUTONUMBER_PREFIX]: ({
		dispatch,
		action: { payload, stopPropagation },
		properties: {
			wizardContext: { app }
		}
	}) => {
		stopPropagation();

		// pushing the current api call into an array in state
		// so that I can use this info later
		currentUniqueChecks = [
			...currentUniqueChecks,
			{
				tableName: 'sys_number',
				fieldName: 'prefix',
				value: payload.value
			}
		];
		checkUniqueField(
			dispatch,
			{
				tableName: 'sys_number',
				fieldName: 'prefix',
				value: payload.value
			},
			app.sys_id
		);
	},
	[CHECK_UNIQUE_FIELD_COMPLETED]: ({
		action: { payload },
		updateState,
		dispatch
	}) => {
		const { unique } = payload;

		// pull off the fieldName and value from the array thats created by the table unique checks
		const { fieldName, value } =
			currentUniqueChecks.length > 0
				? currentUniqueChecks.shift()
				: { fieldName: '', value: '' };

		const path = fieldName === 'prefix' ? 'autonumber.prefix' : 'name';

		if (!unique) {
			dispatch(GENERATE_ERROR_MESSAGES, { unique, value, fieldName, path });
		} else {
			updateState({
				path,
				operation: 'set',
				value
			});
			dispatch(VALIDATE);
		}
	},
	[GENERATE_ERROR_MESSAGES]: ({
		action: {
			payload: { value, fieldName, unique = true, path }
		},
		updateState,
		dispatch
	}) => {
		let message = null;

		if (value && !unique) {
			switch (fieldName) {
				case 'name':
					message = t('Table name already taken');
					break;
				case 'prefix':
					message = t('Prefix name already taken');
					break;
				default:
					break;
			}
		}

		updateState({
			path: `errors.${path}`,
			value: { isDirty: true, message },
			operation: 'set'
		});
		dispatch(VALIDATE);
	},
	[GENERATE_AUTONUMBER_ERRORS]: ({
		state: { autonumber },
		updateState,
		dispatch
	}) => {
		const autonumberErrors = generateAutonumberErrors(autonumber);

		updateState({
			path: 'errors.autonumber',
			value: autonumberErrors,
			operation: 'set'
		});

		if (!autonumberErrors.prefix.message && autonumber.prefix) {
			dispatch(CHECK_TABLE_AUTONUMBER_PREFIX, {
				value: autonumber.prefix
			});
		} else {
			dispatch(VALIDATE);
		}
	},
	[VALIDATE]: ({ updateState }) => {
		updateState(({ state }) => {
			const isFormValid = validateDetails(state);
			return { isFormValid };
		});
	},
	[CREATE_TABLE]: ({
		state: { label, name, isExtendable, enableAutonumber, autonumber },
		properties: {
			wizardContext: { app, table, templateId }
		},
		dispatch,
		updateState
	}) => {
		const reqPayload = {
			template_sys_id: templateId,
			sysparm_transaction_scope: app.sys_id,
			variables: {
				table_data: {
					application: app.sys_id,
					label,
					name,
					is_extendable: isExtendable,
					columns: table.fields || [],
					...(table.tableToExtend && { extends: table.tableToExtend }), // Because `extends` is a reserved key
					...(enableAutonumber && { auto_number: autonumber }),
					domain_separate: false // Default for now until we have this
				}
			}
		};
		dispatch('RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE', reqPayload);
		updateState({ isCreating: true });
	},
	[IMPORT_DATA_REQUESTED]: async ({ action, properties, dispatch }) => {
		const {
			payload: { importSetTableName, tableInfo }
		} = action;
		const {
			app: { sys_id: appId }
		} = properties.wizardContext;

		dispatch(IMPORT_DATA_IN_PROGRESS);

		const response = await importDataFromImportSet(
			appId,
			tableInfo,
			importSetTableName
		);

		if (response.isError) {
			dispatch(IMPORT_DATA_FAILED, response);
			return;
		}

		// NOTE: sending import set response in payload. Can be used to show how many records inserted.
		dispatch(IMPORT_DATA_SUCCEEDED, { table: tableInfo, response });
	},
	[IMPORT_DATA_SUCCEEDED]: ({
		properties: {
			wizardContext: { activeSheetId, sheets }
		},
		action,
		dispatch
	}) => {
		const {
			payload: { table }
		} = action;
		dispatch('SN_GUIDED_WIZARD#CONTINUE', {
			table: table
		});

		const { importSetTableName } = canRunImportSets(activeSheetId, sheets);
		dispatch('SN_GUIDED_WIZARD#IMPORTSET_DONE', { importSetTableName });
	},
	[IMPORT_DATA_FAILED]: ({ action: { payload }, dispatch }) => {
		// Failing to import data into the table that was just created. Something bad has to happen to land here so close the wizard
		dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', payload.error);
	},
	[TABLE_ID_FAILED]: ({ action: { payload }, dispatch }) => {
		// Failing on fetching the sys_id of the table that was just created. If this fails, something REAL bad happened
		dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', payload.error);
	},
	['RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE_SUCCESS']: async ({
		state: { label, name, isExtendable },
		properties: {
			wizardContext: { table, source, activeSheetId, sheets = [] }
		},
		updateState,
		dispatch
	}) => {
		const tableResponse = await getTableSysId(name);

		if (tableResponse.isError) {
			dispatch(TABLE_ID_FAILED, tableResponse);
		}

		const { sysId } = tableResponse;
		const tableInfo = {
			...table,
			label,
			name,
			isExtendable,
			approach: sourceApproachMap[source],
			source,
			sys_id: sysId,
			isSingleSheetUpload: sheets.length === 1
		};

		if (source === 'spreadsheet') {
			// Run Import sets for import case and don't continue
			const { canImport, importSetTableName } = canRunImportSets(
				activeSheetId,
				sheets
			);

			if (canImport) {
				dispatch(IMPORT_DATA_REQUESTED, { importSetTableName, tableInfo });
				return;
			}
		}

		updateState({ isCreating: false });
		dispatch('SN_GUIDED_WIZARD#CONTINUE', {
			table: tableInfo
		});
	},
	['RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE_WARNING']: ({
		updateState,
		action: { payload },
		dispatch
	}) => {
		dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
			...payload.error,
			detail: t('Unable to create a table. Please try again.')
		});
		updateState({ isCreating: false });
	},
	['RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE_FAILURE']: ({
		updateState,
		action: { payload },
		dispatch
	}) => {
		dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', {
			...payload.error,
			detail: t(
				'Unable to create a table. Try starting with a template, again.'
			)
		});
		updateState({ isCreating: false });
	}
};
