import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-checkbox';
import {
	contextHelpers,
	runTemplateBehavior
} from '@devsnc/guided-wizard-toolkit';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

import actionHandlers from './actionHandlers';
import uniqueFieldBehavior from '../../../behaviors/unique-field';
import detailsValidationHelpers from '../../helpers/table-details-validations';
import fieldValidationHelpers from '../../helpers/table-field-validations';
import {
	VALID_CHAR_REGEX_STRING,
	VALID_AUTO_PREFIX_REGEX_STRING,
	PREVENT_ONLY_WHITESPACE_REGEX
} from '../../helpers/error-constants';
import progressTableSVG from '../../../assets/progress-tables.svg';
import {
	CREATE_TABLE,
	DEFAULT_AUTONUMBER_DIGITS,
	DEFAULT_AUTONUMBER_NUMBER
} from './constants';

import styles from './sn-table-wizard-details-page.scss';

const {
	generatePrefixError,
	generateStartingNumberError,
	generateDigitsError
} = detailsValidationHelpers;
const { isAttrInvalid } = fieldValidationHelpers;

const { getContextFromState } = contextHelpers({
	namespace: 'table',
	keys: ['label', 'name', 'isExtendable', 'enableAutonumber', 'autonumber']
});

export const generateAutonumberErrors = autonumberState => {
	// Since the conditional rendering resets the state to what was previously stored,
	// also resetting `isDirty` state by default here
	const defaultErr = { isDirty: false, message: null };
	return {
		prefix: {
			...defaultErr,
			message: generatePrefixError(autonumberState.prefix)
		},
		number: {
			...defaultErr,
			message: generateStartingNumberError(autonumberState.number)
		},
		digits: {
			...defaultErr,
			message: generateDigitsError(autonumberState.digits)
		}
	};
};

const renderAutonumberConfig = state => {
	const {
		autonumber: { prefix, number, digits },
		errors,
		enableAutonumber
	} = state;
	if (!enableAutonumber) {
		return <div className="input-row autonumber" />;
	}

	return (
		<div className="input-row autonumber">
			<now-input
				name="autonumber.prefix"
				className="prefix"
				value={prefix}
				label={t('Prefix')}
				helperContent={generateStyleToolTipContent(
					t(
						'Create an all-caps prefix to abbreviate the table label. For example, for a Laptops table, you may use the prefix "LPTP" or "LT"'
					)
				)}
				autocomplete="off"
				required
				pattern={VALID_AUTO_PREFIX_REGEX_STRING}
				manage-invalid
				invalid={isAttrInvalid(errors.autonumber, 'prefix')}
				messages={
					isAttrInvalid(errors.autonumber, 'prefix')
						? [
								{
									status: 'critical',
									content: errors.autonumber.prefix.message
								}
						  ]
						: []
				}
			/>
			<now-input
				name="autonumber.number"
				className="number"
				value={number}
				type="number"
				label={t('Starting number')}
				helperContent={generateStyleToolTipContent(
					t(
						'Enter the number for the first record in your table. Each new record uses a number that counts up from there.'
					)
				)}
				autocomplete="off"
				required
				min={0}
				step={1}
				manage-invalid
				invalid={isAttrInvalid(errors.autonumber, 'number')}
				messages={
					isAttrInvalid(errors.autonumber, 'number')
						? [
								{
									status: 'critical',
									content: errors.autonumber.number.message
								}
						  ]
						: []
				}
			/>
			<now-input
				name="autonumber.digits"
				className="digits"
				value={digits}
				type="number"
				label={t('Number of digits')}
				helperContent={generateStyleToolTipContent(
					t(
						'Set the digit count of the highest record number. For example, if you enter "7," then the highest number is 9999999.'
					)
				)}
				autocomplete="off"
				required
				min={1}
				max={15}
				step={1}
				manage-invalid
				invalid={isAttrInvalid(errors.autonumber, 'digits')}
				messages={
					isAttrInvalid(errors.autonumber, 'digits')
						? [
								{
									status: 'critical',
									content: errors.autonumber.digits.message
								}
						  ]
						: []
				}
			/>
		</div>
	);
};

const DETAILS_PAGE_WIDTH = `${690 / 16}rem`;

createCustomElement('sn-table-wizard-details-page', {
	view: (state, { dispatch }) => {
		const {
			label,
			name,
			isExtendable,
			enableAutonumber,
			isFormValid,
			isCreating,
			errors,
			properties: { wizardContext, user }
		} = state;

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => dispatch(CREATE_TABLE)}
				primary-disabled={!isFormValid || isCreating}
				running-header-text={t('ADD DATA')}
				headerText={t("Now, let's get more info about your new table")}
				subheaderText={t('Define the properties of your new table.')}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onPrevious={() => {
					const newtable = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', newtable);
				}}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				maxWidth={DETAILS_PAGE_WIDTH}
				loading={isCreating}
				loadingContent={{
					runningHeaderText: t('ADD DATA'),
					headerText: t(
						`Ok {0}. Sit tight while we add this table to your app.`,
						user.firstName
					),
					subheaderText: t('This may take a few minutes...'),
					image: progressTableSVG
				}}>
				<div className="body-container">
					<div className="input-row">
						<now-input
							name="label"
							className="label"
							value={label}
							label={t('Table label')}
							helperContent={generateStyleToolTipContent(
								t(
									'Create a unique label to identify the table (for example, "Laptops" or "Thin Clients")'
								)
							)}
							autocomplete="off"
							required
							pattern={PREVENT_ONLY_WHITESPACE_REGEX}
							manage-invalid
							invalid={isAttrInvalid(errors, 'label')}
							messages={
								isAttrInvalid(errors, 'label')
									? [
											{
												status: 'critical',
												content: errors.label.message
											}
									  ]
									: []
							}
						/>
						<now-input
							name="name"
							className="name"
							value={name}
							label={t('Table name')}
							helperContent={generateStyleToolTipContent(
								t(
									'A name is automatically created after you enter a label. You can edit this name, if needed.'
								)
							)}
							autocomplete="off"
							required
							maxlength={80}
							pattern={VALID_CHAR_REGEX_STRING}
							manage-invalid
							invalid={isAttrInvalid(errors, 'name')}
							messages={
								isAttrInvalid(errors, 'name')
									? [
											{
												status: 'critical',
												content: errors.name.message
											}
									  ]
									: []
							}
						/>
					</div>
					<div className="center-section">
						<sn-aow-checkbox-tooltip
							helperContent={generateStyleToolTipContent(
								t(
									'If you anticipate creating similar tables in the future, you may want to make this extensible. Making this table extensible allows new tables to share the fields and records from this table.'
								)
							)}>
							<now-checkbox
								slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
								label={t('Make extensible')}
								checked={isExtendable}
								name="isExtendable"
								className="isExtendable"
							/>
						</sn-aow-checkbox-tooltip>
						<sn-aow-checkbox-tooltip
							helperContent={generateStyleToolTipContent(
								t(
									'You can add an identifiable tracking number to your records to make them easier to manage and follow. Selecting this option will automatically add your designated tracking number to each record in this table.'
								)
							)}>
							<now-checkbox
								slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
								label={t('Auto number')}
								checked={enableAutonumber}
								name="enableAutonumber"
								className="enableAutonumber"
							/>
						</sn-aow-checkbox-tooltip>
						{renderAutonumberConfig(state)}
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		currentUniqueChecks: [],
		label: '',
		name: '',
		autonumber: {
			prefix: '',
			number: DEFAULT_AUTONUMBER_NUMBER,
			digits: DEFAULT_AUTONUMBER_DIGITS
		},
		errors: {
			label: { isDirty: false, message: null },
			name: { isDirty: false, message: null },
			autonumber: {
				prefix: { isDirty: false, message: null },
				number: { isDirty: false, message: null },
				digits: { isDirty: false, message: null }
			}
		},
		isExtendable: false,
		enableAutonumber: false,
		isFormValid: false,
		isCreating: false
	},
	properties: {
		wizardContext: {},
		stepperItems: {},
		user: { default: '@tableWizard/userDataProperty' }
	},
	actionHandlers,
	behaviors: [
		{
			behavior: runTemplateBehavior.behavior
		},
		{ behavior: uniqueFieldBehavior.behavior }
	],
	styles
});
