import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import {
	contextHelpers,
	tablesDataBehavior
} from '@devsnc/guided-wizard-toolkit';

import '../../components/sn-table-field-list';

import {
	FIELDS_VALIDATED,
	FIELDS_INVALIDATED
} from '../../components/sn-table-field-list/constants';
import styles from './sn-table-wizard-definition-page.scss';

const {
	actionTypes: { TABLES_DATA_BEHAVIOR__FETCH_ALL }
} = tablesDataBehavior;

const { getContextFromState } = contextHelpers({
	namespace: 'table',
	keys: ['fields']
});

createCustomElement('sn-table-wizard-definition-page', {
	view: (state, { dispatch }) => {
		const {
			areFieldsValid,
			properties: { wizardContext }
		} = state;

		const { table } = wizardContext;

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					const newTable = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', newTable);
				}}
				primary-disabled={!areFieldsValid}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('BUILD DATA')}
				headerText={t(
					"Great! Here's the info we brought over from your spreadsheet"
				)}
				subheaderText={t(
					'Make sure you update any necessary field info before we keep going.'
				)}
				onPrevious={() => {
					const newtable = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', {
						...newtable,
						activeSheetId: null
					});
				}}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}>
				<sn-table-field-list
					fields={table ? table.fields : []}
					extendedFields={table ? table.extendedFields : []}
				/>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		areFieldsValid: true,
		fields: []
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: {
				wizardContext: { app }
			},
			dispatch
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-table-wizard-details-page'
			});

			dispatch(TABLES_DATA_BEHAVIOR__FETCH_ALL, {
				sysparm_transaction_scope: app.sys_id
			});
		},
		[FIELDS_VALIDATED]: ({
			action: {
				payload: { fields }
			},
			updateState
		}) => {
			updateState({ fields, areFieldsValid: true });
		},
		[FIELDS_INVALIDATED]: ({ updateState }) => {
			updateState({ areFieldsValid: false });
		}
	},
	styles
});
