import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '../sn-table-field-card';
import '../sn-table-field-card-disabled';
import {
	FIELD_CHANGED,
	FIELD_DELETED,
	TYPE_UPDATED,
	REFERENCE_UPDATED,
	NONE_CHOICE_UPDATED,
	INVALID_SET,
	FIELD_ADDED,
	DUPE_ERR_GENERATED,
	ERRORS_CHECKED,
	FIELDS_VALIDATED,
	FIELDS_INVALIDATED,
	REQUIRED_ERR_MESSAGE,
	ONE_CHOICE_REQUIRED_ERR_MESSAGE,
	FIELD_INPUTTED
} from './constants';

import { CHOICES_UPDATED } from '../sn-table-choices/constants';

import uuid from '../../../helpers/uuid';
import styles from './sn-table-field-list.scss';

import fieldValidationHelpers from '../../helpers/table-field-validations';
const {
	isLabelOrName,
	generateDuplicationErrors,
	updateFieldType,
	updateReference,
	updateChoice,
	validateFields
} = fieldValidationHelpers;

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const DEFAULT_FIELD = {
	name: '',
	label: '',
	type: 'none',
	display: false,
	mandatory: false
};

const DEFAULT_ERRORS_NEW_FIELD = {
	label: { isDirty: false, message: REQUIRED_ERR_MESSAGE },
	name: { isDirty: false, message: REQUIRED_ERR_MESSAGE },
	type: { isDirty: false, message: REQUIRED_ERR_MESSAGE },
	choice: { isDirty: false, message: ONE_CHOICE_REQUIRED_ERR_MESSAGE }
};

// Assuming that if there are fields persisted from properties, they would have to be valid
const DEFAULT_ERRORS_PERSISTED_FIELD = {
	label: { isDirty: false, message: null },
	name: { isDirty: false, message: null },
	type: { isDirty: false, message: null },
	choice: { isDirty: false, message: null }
};

createCustomElement('sn-table-field-list', {
	view: (state, { dispatch, updateState }) => {
		const { fields, errors } = state;
		const { extendedFields = [] } = state.properties;

		return (
			<div>
				<button
					className="add-new-button"
					on-click={() => dispatch(FIELD_ADDED)}>
					<now-icon icon="circle-plus-outline" size="md" />
					{t('Add new field')}
				</button>
				<div
					className="field-cards-container"
					hook-insert={({ elm }) => updateState({ fieldsRef: elm })}
					hook-destroy={() => updateState({ fieldsRef: null })}>
					{fields
						.map(field => (
							<sn-table-field-card
								key={field.id}
								field={field}
								errors={errors[field.id]}
							/>
						))
						.reverse()}
					{extendedFields.map(extendedField => (
						<sn-table-field-card-disabled
							key={extendedField.id}
							field={extendedField}
						/>
					))}
				</div>
			</div>
		);
	},
	initialState: {
		fields: [],
		errors: {},
		fieldsRef: null
	},
	properties: {
		fields: {
			default: []
		},
		extendedFields: {
			default: []
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ properties, updateState, dispatch }) => {
			let errors = {};

			const fields = properties.fields.map(f => {
				const id = uuid();
				errors[id] = JSON.parse(JSON.stringify(DEFAULT_ERRORS_PERSISTED_FIELD));
				// If spreadsheet don't have data, API call gives `internal_type` as null. So `Continue` button should be disabled.
				// Based on errors, `ERRORS_CHECKED` effect dispatches `FIELDS_INVALIDATED` action to disable the `Continue` button
				if (!f.type) {
					errors[id].type.message = DEFAULT_ERRORS_NEW_FIELD.type.message;
				}
				return { ...f, id };
			});

			updateState({ fields, errors });
			dispatch(ERRORS_CHECKED);
		},
		[FIELD_INPUTTED]: {
			modifier: { name: 'debounce', delay: 200 },
			// batch sequence of characters user enters
			effect: ({
				action: {
					payload: { attr, field }
				},
				updateState
			}) => {
				// here we just reset message, the FIELD_CHANGED will set message after validation
				updateState({
					path: `errors[${field.id}][${attr}][message]`,
					value: null,
					operation: 'set'
				});
			}
		},
		[FIELD_CHANGED]: ({
			state: { fields },
			action: {
				payload: { field, attr }
			},
			updateState,
			dispatch
		}) => {
			const newFields = fields.map(f => (f.id === field.id ? { ...field } : f));
			updateState({ fields: newFields });
			if (isLabelOrName(attr)) {
				return dispatch(DUPE_ERR_GENERATED, { field, attr });
			}
			dispatch(ERRORS_CHECKED);
		},
		[FIELD_ADDED]: ({
			state: { fields, errors, fieldsRef },
			updateState,
			dispatch
		}) => {
			// Provide unique identifier
			const id = uuid();

			updateState({
				fields: fields.concat({
					...DEFAULT_FIELD,
					id
				}),
				errors: {
					...errors,
					[id]: JSON.parse(JSON.stringify(DEFAULT_ERRORS_NEW_FIELD))
				}
			});
			dispatch(ERRORS_CHECKED);
			fieldsRef.scrollTop = 0;
		},
		[FIELD_DELETED]: ({
			state: { fields, errors },
			action: {
				payload: { id }
			},
			updateState,
			dispatch
		}) => {
			const newFields = fields.filter(i => i.id !== id);
			const newErrors = { ...errors };
			delete newErrors[id];
			updateState({ fields: newFields, errors: newErrors });
			// Since the field deleted may have been triggering a duplication error...
			dispatch(DUPE_ERR_GENERATED, {});
		},
		[TYPE_UPDATED]: ({
			state: { fields, errors },
			action: {
				payload: { field }
			},
			updateState,
			dispatch
		}) => {
			const { newFields, newErrors } = updateFieldType(field, fields, errors);
			updateState({
				fields: newFields,
				errors: newErrors
			});
			dispatch(ERRORS_CHECKED);
		},
		[REFERENCE_UPDATED]: ({
			state: { fields, errors },
			action: {
				payload: { field }
			},
			updateState,
			dispatch
		}) => {
			const { newFields, newErrors } = updateReference(field, fields, errors);
			updateState({
				fields: newFields,
				errors: newErrors
			});
			dispatch(ERRORS_CHECKED);
		},
		[NONE_CHOICE_UPDATED]: ({
			state: { fields, errors },
			action: {
				payload: { field }
			},
			updateState,
			dispatch
		}) => {
			const { newFields, newErrors } = updateChoice({
				field,
				fields,
				errors
			});

			updateState({
				fields: newFields,
				errors: newErrors
			});
			dispatch(ERRORS_CHECKED);
		},
		[CHOICES_UPDATED]: ({
			state: { fields, errors },
			action: {
				payload: { field }
			},
			updateState,
			dispatch
		}) => {
			const fieldsWithNewChoices = [...fields];
			const fieldIdx = fields.findIndex(f => f.id === field.id);
			fieldsWithNewChoices[fieldIdx].choices = field.choices;

			const { newFields, newErrors } = updateChoice({
				field,
				fields: fieldsWithNewChoices,
				errors
			});

			updateState({
				fields: newFields,
				errors: newErrors
			});

			dispatch(ERRORS_CHECKED);
		},
		[INVALID_SET]: ({
			action: {
				payload: { field, attr, message }
			},
			updateState,
			dispatch
		}) => {
			updateState({
				path: `errors.${field.id}.${attr}`,
				value: { isDirty: true, message },
				operation: 'set'
			});

			if (isLabelOrName(attr) && !message) {
				/**
				 * Generating duplication errors is pretty expensive so let's only do it if:
				 * 1. It's either the `label` or `name` key changing **AND**
				 * 2. The native input is valid (i.e. this action sends up a `null` message)
				 *
				 * Folks with keen eyes will notice that this case will cause
				 * GENERATE_DUPLICATION_ERRORS to be dispatched both from here and from the
				 * FIELD_CHANGED action. Hence the debounce below to rely on the latest call.
				 */
				return dispatch(DUPE_ERR_GENERATED, { field, attr });
			}
			dispatch(ERRORS_CHECKED);
		},
		[DUPE_ERR_GENERATED]: {
			modifier: { name: 'debounce', delay: 100 },
			// Because FIELD_CHANGED and INVALID_SET can independently dispatch this action
			effect: ({
				state: { fields, errors },
				properties: { extendedFields },
				action: { payload },
				updateState,
				dispatch
			}) => {
				// When a field is deleted there won't be a field in the payload
				const { field = {} } = payload;
				const newErrors = generateDuplicationErrors(
					field,
					fields,
					errors,
					extendedFields
				);
				updateState({ errors: newErrors });
				dispatch(ERRORS_CHECKED);
			}
		},
		[ERRORS_CHECKED]: {
			modifier: { name: 'debounce', delay: 200 },
			// Because a whole bunch of actions can independently dispatch this one
			effect: ({ dispatch }) => {
				dispatch(({ state: { fields, errors } }) => {
					const isValid = validateFields(errors);

					if (isValid) {
						// If all is good, strip out the id and send up the fields!
						const fieldsWithoutIds = fields.map(
							// eslint-disable-next-line
							({ id, choices, choice, ...rest }) => {
								const doesChoicesExist = choices && choices.length > 0;
								return {
									...rest,
									// put back choices in the object
									// just need it to see what the selection is
									// down below
									...(choice && {
										choice
									}),
									// only adds a choices property to the fields without
									// if the user added a choice
									// otherwise there is no choices property
									...(doesChoicesExist && {
										choices: choices.map(
											// eslint-disable-next-line
											({ id, ...restChoices }) => ({
												...restChoices
											})
										)
									}),
									// only add on a default_value if user choose
									// dropdown wihout none and has added at least once choice
									...(doesChoicesExist &&
										choice === '3' && {
											default_value: choices[0].value
										})
								};
							}
						);

						return {
							type: FIELDS_VALIDATED,
							payload: { fields: fieldsWithoutIds }
						};
					} else {
						// Don't send up the fields to the page if they aren't valid
						return {
							type: FIELDS_INVALIDATED
						};
					}
				});
			}
		}
	},
	styles
});
