import fetchHelper, { failedResponse } from '../../../helpers/fetch-helper';
import { serializeSNName } from '../../../helpers/sn-naming-helpers';
import token from '../../../helpers/get-auth-token';
import { t } from 'sn-translate';
import { waitForWorker } from '../../../helpers/wait-for-util';

const getSheetColumnsAPI = async (
	appId,
	attachmentId,
	sheetNumber,
	headerNumber
) =>
	await fetchHelper(
		`/api/sn_table_builder/app/excel_spreadsheet_columns?sysparm_transaction_scope=${appId}&attachmentSysId=${attachmentId}&headerRow=${headerNumber}&sheetNumber=${sheetNumber}`
	);

const getColumnsFromImportedTableAPI = async (appId, table) =>
	await fetchHelper(
		`/api/now/ui/meta/${table}?sysparm_transaction_scope=${appId}&sysparm_is_extended_table=true`
	);

const deleteRecordAPI = async (table, recordId, appId) =>
	await fetchHelper(
		`/api/now/table/${table}/${recordId}?sysparm_transaction_scope=${appId}`,
		{
			method: 'DELETE'
		}
	);

export const getColumnsFromSheet = async ({
	appId,
	attachmentId,
	headerNumber,
	sheetNumber
}) => {
	const response = await getSheetColumnsAPI(
		appId,
		attachmentId,
		sheetNumber,
		headerNumber
	);

	if (response.isError) {
		return response;
	}

	return { fields: transformColumns(response.result.columns) };
};

export const uploadSheetWithData = async ({
	appId,
	headerNumber,
	sheetNumber,
	file,
	importSetTableName
}) => {
	const formData = buildFormData(
		file,
		headerNumber,
		sheetNumber,
		importSetTableName
	);

	// NOTE: sys_import.do is successful but not re-directing in 8081, so no progress id to wait for.
	try {
		const response = await fetch(
			`/sys_import.do?sysparm_transaction_scope=${appId}`,
			{
				method: 'POST',
				body: formData,
				redirect: 'follow'
			}
		);
		const id = new URL(response.url).searchParams.get('sysparm_pworker_sysid');

		// NOTE: angular.do is failing in 8081 so faking delay in 8081
		process.env.NODE_ENV === 'development'
			? await delay(2000)
			: await waitHelper(id);
	} catch (err) {
		return failedResponse(err.message);
	}

	// Need to fetch from import table so that we can map during actual import
	const columnResponse = await getColumnsFromImportedTableAPI(
		appId,
		importSetTableName
	);

	if (columnResponse.isError) {
		return columnResponse;
	}

	return { fields: transformColumns(columnResponse.result.columns, true) };
};

async function waitHelper(id) {
	if (!id) {
		return failedResponse(t('Could not find progress worker for import.'));
	}
	await waitForWorker(id);
}

function transformColumns(columns, isImportSet) {
	return Object.keys(columns).map(fieldName => {
		const item = columns[fieldName];
		const name = serializeSNName(isImportSet ? item.label : fieldName);
		return {
			name,
			label: item.label,
			type: item.internal_type,
			length: item.max_length,
			...(isImportSet ? { initialName: fieldName } : {}) // use initialName during import set transform
		};
	});
}

function buildFormData(file, headerNumber, sheetNumber, importSetTableName) {
	const formData = new FormData();
	formData.append('attachFile', file);
	formData.append('header_row', headerNumber);
	formData.append('import_action', 'create_table');
	formData.append('import_source', 'file_upload');
	formData.append('max_rows', '-1');
	formData.append('sheet', sheetNumber);
	formData.append('sysparm_ck', token['X-UserToken']);
	formData.append('sysparm_extends', 'sys_import_set_row');
	formData.append('sysparm_import_set_tablename', importSetTableName);
	formData.append('sysparm_tablelabel', importSetTableName);
	return formData;
}

async function delay(value) {
	return new Promise(res => {
		setTimeout(() => {
			res();
		}, value);
	});
}

/**
 * Update specific sheet with given data
 * @param {Array} sheets list of sheets
 * @param {String} name sheet name to be updated
 * @param {Object} data data to be updated in sheet
 * @returns {Array} updated sheets
 */
export function updateSheetData(sheets, name, data) {
	return sheets.map(sh => {
		if (sh.name === name) {
			return {
				...sh,
				...data
			};
		}
		return sh;
	});
}

export async function doCleanUpRecords(cleanUpRecords, appId) {
	// deleting helperRecord also deletes attachment
	for (const recordId in cleanUpRecords) {
		await deleteRecordAPI(cleanUpRecords[recordId], recordId, appId);
	}
}
