import {
	REQUIRED_ERR,
	NO_LOWERCASE_ERR,
	HAS_LOWERCASE_REGEX_STRING,
	INVALID_CHAR_ERR,
	MIN_STARTING_NUMBER_ERR,
	MIN_AUTO_DIGITS_ERR,
	MAX_AUTO_DIGITS_ERR,
	NO_PUNCTUATION_ERR,
	VALID_AUTO_PREFIX_REGEX_STRING,
	NO_DOTS_REGEX_STRING
} from './error-constants';

// Generate a valid autonumbering prefix based on table label
const getAutonumberPrefix = label => {
	return label
		.replace(/[^a-zA-Z]/g, '')
		.toUpperCase()
		.slice(0, 3);
};

const generatePrefixError = prefix => {
	if (!prefix) {
		return REQUIRED_ERR;
	}

	// Special case for lowercase characters
	const hasLowercase = new RegExp(HAS_LOWERCASE_REGEX_STRING);
	if (hasLowercase.exec(prefix)) {
		return NO_LOWERCASE_ERR;
	}

	// Default invalid char case to capture generic mismatch
	const validRegex = new RegExp(VALID_AUTO_PREFIX_REGEX_STRING);
	if (!validRegex.exec(prefix)) {
		return INVALID_CHAR_ERR;
	}

	return null;
};

const generateStartingNumberError = startingNumber => {
	if (!startingNumber) {
		return REQUIRED_ERR;
	}

	if (+startingNumber < 0) {
		return MIN_STARTING_NUMBER_ERR;
	}

	const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
	if (!validRegex.exec(startingNumber)) {
		return NO_PUNCTUATION_ERR;
	}

	return null;
};

const generateDigitsError = digits => {
	if (!digits) {
		return REQUIRED_ERR;
	}

	if (+digits < 1) {
		return MIN_AUTO_DIGITS_ERR;
	}

	if (+digits > 15) {
		return MAX_AUTO_DIGITS_ERR;
	}

	const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
	if (!validRegex.exec(digits)) {
		return NO_PUNCTUATION_ERR;
	}

	return null;
};

const validateDetails = ({ enableAutonumber, errors }) => {
	for (const key in errors) {
		if (enableAutonumber && key === 'autonumber') {
			for (const autonumberKey in errors.autonumber) {
				if (typeof errors.autonumber[autonumberKey].message === 'string') {
					return false;
				}
			}
		}

		if (typeof errors[key].message === 'string') {
			return false;
		}
	}

	return true;
};

export default {
	getAutonumberPrefix,
	generatePrefixError,
	generateStartingNumberError,
	generateDigitsError,
	validateDetails
};
