import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-checkbox';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

import {
	generateStartingNumberError,
	validateDetails
} from '../../helpers/spreadsheet-row-validation';
import fieldValidationHelpers from '../../helpers/table-field-validations';

import { NO_DOTS_REGEX_STRING } from '../../helpers/error-constants';

import styles from './sn-table-wizard-spreadsheet-page.scss';
import { uploadFileToHelper, getSheetNames } from './utils';
import {
	UPLOAD_FILE_TO_HELPER_REQUESTED,
	UPLOAD_FILE_TO_HELPER_IN_PROGRESS,
	UPLOAD_FILE_TO_HELPER_FAILED,
	UPLOAD_FILE_TO_HELPER_SUCCEEDED,
	HELPER_TABLE,
	PARSE_FILE_REQUESTED,
	PARSE_FILE_IN_PROGRESS,
	PARSE_FILE_SUCCEEDED,
	PARSE_FILE_FAILED
} from './constants';
import { VALIDATE } from '../../constants';

const { isAttrInvalid } = fieldValidationHelpers;

createCustomElement('sn-table-wizard-spreadsheet-page', {
	view: (state, { dispatch }) => {
		const {
			headerNumber,
			importDataCheckbox,
			sheets,
			attachmentId,
			inProgress,
			processLabel,
			fileParsed,
			errors,
			isFormValid,
			properties: {
				wizardContext: {
					fileUpload = {
						hasSelectedFile: false,
						selectedFile: null
					}
				}
			}
		} = state;

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						headerNumber,
						importDataCheckbox,
						fileUpload,
						sheets,
						attachmentId
					});
				}}
				primaryDisabled={!fileParsed || !isFormValid}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('ADD DATA')}
				headerText={
					fileUpload.hasSelectedFile
						? t("Nice choice. Here's what you chose")
						: t("Let's choose the spreadsheet you want to upload")
				}
				subheaderText={
					fileUpload.hasSelectedFile
						? t("Next, we're going to configure the column headers.")
						: t(
								"This spreadsheet will be a table in your app. If there are multiple worksheets in your spreadsheet, we'll use the first one to create the table."
						  )
				}
				onPrevious={() => dispatch('SN_GUIDED_WIZARD#PREVIOUS')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				maxWidth="680px">
				<sn-aow-file-uploader-spreadsheet
					selectedFile={fileUpload.selectedFile}
					label={t('Drag and drop or browse to upload spreadsheet')}
					loading={inProgress}
					loadingText={processLabel}
				/>
				{fileUpload.hasSelectedFile && sheets.length === 1 ? (
					<div className="sn-table-spreadsheet-inputs">
						<now-input
							className="header-number-input"
							name="headerNumber"
							value={headerNumber}
							label={t('Enter a row number for the table header')}
							size="lg"
							autocomplete="off"
							type="number"
							min={1}
							step={1}
							required
							pattern={NO_DOTS_REGEX_STRING}
							helperContent={generateStyleToolTipContent(
								t(
									'Enter the row number in which the column headers are located. For example, if the column headers are in the second row of your spreadsheet, you would enter 2.'
								)
							)}
							messages={
								isAttrInvalid(errors, 'headerNumber')
									? [
											{
												status: 'critical',
												content: errors.headerNumber.message
											}
									  ]
									: []
							}
						/>
						<sn-aow-checkbox-tooltip
							helperContent={generateStyleToolTipContent(
								t(
									'To include the data that is after the column headers in your spreadsheet, select this option'
								)
							)}>
							<now-checkbox
								slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
								className="import-data-checkbox"
								name="importDataCheckbox"
								label={t('Import spreadsheet data')}
								checked={importDataCheckbox}
							/>
						</sn-aow-checkbox-tooltip>
					</div>
				) : null}
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		headerNumber: 1,
		importDataCheckbox: false,
		sheets: [],
		attachmentId: undefined,
		inProgress: false,
		processLabel: '',
		fileParsed: false,
		errors: {
			headerNumber: {
				message: null
			}
		},
		isFormValid: true
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({
			properties: {
				wizardContext: {
					fileUpload = {},
					sheets,
					attachmentId,
					importDataCheckbox,
					headerNumber
				}
			},
			updateState
		}) => {
			if (fileUpload.hasSelectedFile) {
				updateState({
					sheets,
					attachmentId,
					importDataCheckbox,
					headerNumber,
					fileParsed: true
				});
			}
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_CHECKBOX#CHECKED_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_INPUT#INVALID_SET']: ({
			action: { payload, stopPropagation },
			updateState,
			dispatch
		}) => {
			stopPropagation();
			const { name, fieldValue } = payload;
			let message = null;

			if (name === 'headerNumber') {
				message = generateStartingNumberError(fieldValue);
			}

			// INVALID_SET is fired on every input blur, can reliably capture `isDirty` state
			updateState({
				path: `errors.${name}`,
				value: { isDirty: true, message },
				operation: 'set'
			});

			dispatch(VALIDATE);
		},
		[VALIDATE]: ({ updateState }) => {
			updateState(({ state }) => {
				const isFormValid = validateDetails(state);
				return { isFormValid };
			});
		},
		['AOW_FILE_UPLOADER#FILE_SET']: ({
			properties: {
				wizardContext: { fileUpload }
			},
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				fileUpload: {
					...fileUpload,
					selectedFile: payload,
					hasSelectedFile: !!payload
				}
			});

			dispatch(UPLOAD_FILE_TO_HELPER_REQUESTED, payload);
		},
		[UPLOAD_FILE_TO_HELPER_REQUESTED]: async ({
			action: { payload },
			dispatch,
			properties: { wizardContext }
		}) => {
			dispatch(UPLOAD_FILE_TO_HELPER_IN_PROGRESS);

			const response = await uploadFileToHelper(
				payload,
				wizardContext.app.sys_id
			);

			if (response.isError) {
				dispatch(UPLOAD_FILE_TO_HELPER_FAILED, response);
				return;
			}

			const { attachmentId, helperRecordId } = response;
			// Note: Use helperRecordId for cleanup, we should not delete record upon `delete` button.
			// we need that record in next pages. So created cleanUpRecords do it at the end. recordId and table pair
			const { cleanUpRecords = {} } = wizardContext;
			const newCleanUpRecords = {
				...cleanUpRecords,
				[helperRecordId]: HELPER_TABLE
			};
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				cleanUpRecords: newCleanUpRecords
			});
			dispatch(UPLOAD_FILE_TO_HELPER_SUCCEEDED, {
				attachmentId,
				helperRecordId
			});
		},
		[UPLOAD_FILE_TO_HELPER_IN_PROGRESS]: ({ updateState }) => {
			updateState({
				inProgress: true,
				processLabel: t('Uploading file')
			});
		},
		[UPLOAD_FILE_TO_HELPER_SUCCEEDED]: ({ action, updateState, dispatch }) => {
			const {
				payload: { attachmentId }
			} = action;
			updateState({ attachmentId });
			dispatch(PARSE_FILE_REQUESTED, { attachmentId });
		},
		[UPLOAD_FILE_TO_HELPER_FAILED]: ({
			action: { payload },
			dispatch,
			updateState
		}) => {
			// Failing to upload the spreadsheet as an attachment
			dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
				...payload.error,
				detail: t('Unable to upload file. {0}', payload.error.message)
			});
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				fileUpload: {
					selectedFile: null,
					hasSelectedFile: false
				},
				sheets: [],
				table: {}
			});
			updateState({
				sheets: [],
				attachmentId: undefined,
				inProgress: false,
				processLabel: t('Upload failed'),
				fileParsed: false
			});
		},
		[PARSE_FILE_REQUESTED]: async ({
			action: { payload },
			dispatch,
			properties: { wizardContext }
		}) => {
			dispatch(PARSE_FILE_IN_PROGRESS);

			const { attachmentId } = payload;

			const response = await getSheetNames(
				attachmentId,
				wizardContext.app.sys_id
			);

			if (response.isError) {
				dispatch(PARSE_FILE_FAILED, response);
				return;
			}

			const { sheets } = response;
			dispatch(PARSE_FILE_SUCCEEDED, { sheets });
		},
		[PARSE_FILE_IN_PROGRESS]: ({ updateState }) => {
			updateState({
				inProgress: true,
				processLabel: t('Parsing file')
			});
		},
		[PARSE_FILE_SUCCEEDED]: ({
			action: { payload },
			updateState,
			dispatch
		}) => {
			const { sheets } = payload;
			updateState({
				sheets,
				inProgress: false,
				fileParsed: true
			});
			dispatch(VALIDATE);
		},
		[PARSE_FILE_FAILED]: ({ action: { payload }, dispatch, updateState }) => {
			// Failing to determine how many sheets are in the file
			dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
				...payload.error,
				detail: t(
					'Unable to go through the multiple worksheets within the file.'
				)
			});
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				fileUpload: {
					selectedFile: null,
					hasSelectedFile: false
				},
				sheets: [],
				table: {}
			});
			updateState({
				sheets: [],
				attachmentId: undefined,
				inProgress: false,
				processLabel: t('Parse failed'),
				fileParsed: false,
				errors: {
					headerNumber: {
						isDirty: false,
						message: null
					}
				}
			});
		},
		['AOW_FILE_UPLOADER#FILE_REMOVE']: ({ dispatch, updateState }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				fileUpload: {
					selectedFile: null,
					hasSelectedFile: false
				},
				sheets: [],
				table: {}
			});
			updateState({
				fileParsed: false,
				sheets: [],
				attachmentId: undefined
			});
		},
		['AOW_FILE_UPLOADER#FILETYPE_ERROR']: ({ dispatch, action: { payload } }) => {
			dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
				detail: payload.error
			});
			// Not even sure if this action is ever dispatched but just to be sure:
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				fileUpload: {
					selectedFile: null,
					hasSelectedFile: false
				},
				sheets: [],
				table: {}
			});
		}
	},
	styles
});
