import authToken from './get-auth-token';
import { t } from 'sn-translate';

/**
 * helper for fetch.
 * return response with isError flag, will be true states non-2xx status
 * @param {string} url
 * @param {Object} options
 */
export default async function fetchHelper(url, options) {
	const response = await fetch(url, {
		method: 'GET',
		headers: {
			'Content-Type': 'application/json',
			...authToken
		},
		...options
	})
		.then(async response => {
			return {
				isError: !response.ok,
				...(await response.json())
			};
		})
		.catch(() => {
			return failedResponse();
		});

	return response;
}

export function failedResponse(msg) {
	return {
		isError: true,
		// simulate SNOW error
		error: {
			message: msg ? msg : t('Failed to load'),
			detail: ''
		},
		status: 'failure'
	};
}
