import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-button';

import styles from '../sn-table-field-card/sn-table-field-card.scss';
import flagStyles from './sn-table-field-card-disabled.scss';

const renderConditionalInput = ({ field }) => {
	switch (field.type) {
		case 'string':
			return (
				<now-input
					className="additional-input length"
					type="number"
					label={t('Character limit')}
					helper-content={t(
						'Max length of the strings that can be stored here.'
					)}
					value={field.length}
					min={1}
					max={8000}
					step={1}
					disabled
				/>
			);
		case 'reference':
			return (
				<now-input
					className="additional-input reference-dropdown"
					value={field.reference}
					label={t('Reference table')}
					helper-content={t('INSERT REFERENCE TOOLTIP HERE')}
					maxlength={80}
					disabled
				/>
			);
		default:
			return '';
	}
};

createCustomElement('sn-table-field-card-disabled', {
	view: state => {
		const { field } = state.properties;
		const { name, label, type } = field;

		const dropdownType = typeof type === 'undefined' ? 'none' : type;

		// These are all now-inputs to remove rendering the panel of the dropdown, greatly improves performance
		return (
			<div className="table-field-card-container is-disabled">
				<div className="red-flag">
					<now-icon icon="lock-fill" size="sm" />
				</div>
				<div className="main-content">
					<div className="inputs-wrapper">
						<now-input
							className="label"
							value={label}
							label={t('Field label')}
							helper-content={t(
								'The field label appears on list headers and form fields for the column.'
							)}
							maxlength={80}
							disabled
						/>
						<now-input
							className="name"
							value={name}
							label={t('Field name')}
							helper-content={t('The physical field name within the table.')}
							maxlength={80}
							disabled
						/>
						<now-input
							className="type"
							value={dropdownType}
							label={t('Field type')}
							helper-content={t('INSERT FIELD TYPE TOOLTIP')}
							maxlength={80}
							disabled
						/>
						{renderConditionalInput(state.properties)}
					</div>

					<div className="now-align -block-center">
						<now-button-iconic
							className="expand"
							bare
							icon="chevron-down-fill"
							disabled
						/>
					</div>
				</div>
			</div>
		);
	},
	properties: {
		field: {
			default: {}
		}
	},
	styles: [styles, flagStyles]
});
