import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-heading';
import '@servicenow/now-modal';
import '@devsnc/guided-wizard-toolkit';
import './components/sn-tb-spreadsheet-info-card';
import { SPREADSHEET_INFO_CARD__CONVERT_TO_TABLE } from './components/sn-tb-spreadsheet-info-card/constants';
import { SHEET_COMPLETION_STATES, getSheetCompletion } from './helpers';
import styles from './sn-table-wizard-spreadsheet-list-page.scss';
import {
	getColumnsFromSheet,
	uploadSheetWithData,
	updateSheetData,
	doCleanUpRecords
} from './utils';
import {
	GET_SHEET_COLUMNS_REQUESTED,
	GET_SHEET_COLUMNS_IN_PROGRESS,
	GET_SHEET_COLUMNS_SUCCEEDED,
	GET_SHEET_COLUMNS_WITH_DATA_REQUESTED,
	GET_SHEET_COLUMNS_WITH_DATA_IN_PROGRESS,
	GET_SHEET_COLUMNS_WITH_DATA_SUCCEEDED,
	GET_SHEET_COLUMNS_WITH_DATA_FAILED,
	GET_SHEET_COLUMNS_FAILED,
	CLEAN_UP_RECORDS
} from './constants';

import parsingIllustrationSVG from './assets/parsing-illustration.svg';

// Only `partial` needs to be a function but to keep the interface clean...
const headerTextCopy = {
	incomplete: () => t('Looks like your spreadsheet has multiple worksheets'),
	partial: tableName =>
		t('Success! The "{0}" table has been added to your app', tableName),
	complete: () => t('Success! Here are your new tables')
};

const subheaderTextCopy = {
	incomplete: t(
		"Choose a worksheet to convert into a table. When you're done converting one of the sheets, we'll bring you back to this screen to convert more sheets."
	),
	partial: t(
		'Now, you can either convert more sheets into tables, edit the tables you just added or leave to continue building the rest of your app. If you do leave, you can always come back to convert more sheets by uploading your spreadsheet again.'
	),
	complete: t(
		'You can view or add more data to your tables by selecting "Edit tables."'
	)
};

export const actionHandlers = {
	[actionTypes.COMPONENT_DOM_READY]: ({
		// Use DOM_READY as it is higher priority action. There will be visible difference running on slow machine
		properties,
		dispatch,
		updateState
	}) => {
		const {
			sheets,
			activeSheetId,
			table,
			createdTables = []
		} = properties.wizardContext;

		// AFTER A SHEET IS COVERTED TO A TABLE AND THIS PAGE IS SHOWN AGAIN

		// Checks if a spreadsheet was just converted to table
		if (activeSheetId) {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				createdTables: createdTables.concat(table)
			});
			updateState({ lastCreatedTableName: table.label }); // Only used for `partial` completion case

			const updatedSheets = updateSheetData(sheets, activeSheetId, { table });
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', {
				// update the sheet object to include the table output
				sheets: updatedSheets,
				activeSheetId: null,
				table: {}
			});

			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-table-wizard-summary-page'
			});

			const { completionState } = getSheetCompletion(updatedSheets);
			if (completionState === SHEET_COMPLETION_STATES.COMPLETE) {
				dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');
			}

			return;
		}

		dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
			page: 'sn-table-wizard-details-page'
		});

		// SINGLE SHEET USE-CASE (before configuring table)
		if (sheets.length === 1 && !sheets[0].table.sys_id) {
			const {
				sheets,
				headerNumber,
				importDataCheckbox
			} = properties.wizardContext;

			updateState({ isSingleSheet: true });
			dispatch(SPREADSHEET_INFO_CARD__CONVERT_TO_TABLE, {
				sheet: sheets[0],
				headerNumber,
				importDataCheckbox
			});
		}
	},
	['NOW_MODAL#OPENED_SET']: ({
		state: { modalState },
		updateState,
		action
	}) => {
		action.stopPropagation();
		updateState({
			modalState: {
				opened: !modalState.opened,
				case: action.payload.case
			}
		});
	},
	['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({
		state: { modalState },
		properties: {
			wizardContext: { createdTables }
		},
		updateState,
		dispatch,
		action
	}) => {
		action.stopPropagation();
		if (action.payload.action.id === 'secondary') {
			updateState({
				modalState: {
					opened: false,
					case: null
				}
			});
			return;
		}

		switch (modalState.case) {
			case 'complete':
				dispatch('SN_GUIDED_WIZARD#COMPLETE', {
					key: 'sys_db_object',
					result: createdTables
				});
				break;
			case 'edit':
				dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
					key: 'sys_db_object',
					result: createdTables
				});
				break;
			default:
				break;
		}
	},
	[SPREADSHEET_INFO_CARD__CONVERT_TO_TABLE]: ({
		action,
		dispatch,
		updateState
	}) => {
		const {
			payload: { sheet, headerNumber, importDataCheckbox }
		} = action;
		updateState({ sheetNameToCreate: sheet.name });

		if (importDataCheckbox) {
			dispatch(GET_SHEET_COLUMNS_WITH_DATA_REQUESTED, {
				sheet,
				headerNumber
			});
		} else {
			dispatch(GET_SHEET_COLUMNS_REQUESTED, { sheet, headerNumber });
		}
	},
	[GET_SHEET_COLUMNS_REQUESTED]: async ({ properties, action, dispatch }) => {
		const {
			payload: { sheet, headerNumber }
		} = action;
		const {
			app: { sys_id: appId },
			attachmentId
		} = properties.wizardContext;

		dispatch(GET_SHEET_COLUMNS_IN_PROGRESS);

		const response = await getColumnsFromSheet({
			appId,
			attachmentId,
			headerNumber,
			sheetNumber: sheet.number
		});

		if (response.isError) {
			dispatch(GET_SHEET_COLUMNS_FAILED, response); // AOW - We need  to handle error action, payload includes error object
			return;
		}

		dispatch(GET_SHEET_COLUMNS_SUCCEEDED, {
			fields: response.fields,
			sheet
		});
	},
	[GET_SHEET_COLUMNS_IN_PROGRESS]: ({ updateState }) => {
		updateState({ isConversionInProgress: true });
	},
	[GET_SHEET_COLUMNS_SUCCEEDED]: ({ action, dispatch, updateState }) => {
		const {
			payload: { sheet, fields }
		} = action;
		const table = { ...sheet.table, fields };

		updateState({ isConversionInProgress: false });

		// Ultimately need to send the fields into the definition page
		dispatch('SN_GUIDED_WIZARD#CONTINUE', {
			table,
			activeSheetId: sheet.name
		});
	},
	[GET_SHEET_COLUMNS_FAILED]: ({
		action: { payload },
		dispatch,
		properties: {
			wizardContext: { sheets }
		},
		updateState
	}) => {
		// The columns have failed to parse
		const { completionState, incompleteSheets } = getSheetCompletion(sheets);

		// If there have been any sheets previously completed
		if (completionState === SHEET_COMPLETION_STATES.PARTIAL) {
			// Treat it as a CRITICAL case and close the wizard
			dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', {
				...payload.error,
				detail: t(
					'Unable to convert the worksheet you chose. Try starting with a template, again.'
				)
			});
			return;
		}

		// If it's the first sheet to be parsed, just display an error and allow the user to try again
		dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
			...payload.error,
			detail: t('Unable to prepare the column headers for review.')
		});

		// And finally, if it's a single, bring the user back to the spreadsheet upload page (skipping this page)
		if (incompleteSheets.length === 1) {
			dispatch('SN_GUIDED_WIZARD#PREVIOUS');
		} else {
			// Otherwise set `isConversionInProgress` to false
			updateState({ isConversionInProgress: false });
		}
	},
	[GET_SHEET_COLUMNS_WITH_DATA_REQUESTED]: async ({
		properties,
		action,
		dispatch
	}) => {
		const {
			payload: { sheet, headerNumber }
		} = action;
		const {
			app: { sys_id: appId, scope },
			fileUpload: { selectedFile: file },
			attachmentId
		} = properties.wizardContext;

		dispatch(GET_SHEET_COLUMNS_WITH_DATA_IN_PROGRESS);

		const importSetTableName = `${scope}_${Date.now()}`;
		const response = await uploadSheetWithData({
			appId,
			attachmentId,
			headerNumber,
			sheetNumber: sheet.number,
			file,
			importSetTableName
		});

		if (response.isError) {
			dispatch(GET_SHEET_COLUMNS_WITH_DATA_FAILED, response); // AOW - We need handle error action, payload includes error object
			return;
		}

		dispatch(GET_SHEET_COLUMNS_WITH_DATA_SUCCEEDED, {
			fields: response.fields,
			sheet,
			importSetTableName
		});
	},
	[GET_SHEET_COLUMNS_WITH_DATA_IN_PROGRESS]: ({ updateState }) => {
		updateState({ isConversionInProgress: true });
	},
	[GET_SHEET_COLUMNS_WITH_DATA_SUCCEEDED]: ({
		action,
		updateState,
		dispatch,
		properties
	}) => {
		const {
			payload: { sheet, fields, importSetTableName }
		} = action;
		const table = { ...sheet.table, fields };
		const { sheets } = properties.wizardContext;

		updateState({ isConversionInProgress: false });

		dispatch('SN_GUIDED_WIZARD#IMPORTSET_PROCESSING', { importSetTableName });

		// Ultimately need to send the fields into the definition page
		dispatch('SN_GUIDED_WIZARD#CONTINUE', {
			table,
			sheets: updateSheetData(sheets, sheet.name, {
				// Adding importSetTableName to sheet and set in context, used to import data during table creation
				importSetTableName,
				importDataCheckbox: true
			}),
			activeSheetId: sheet.name
		});
	},
	[GET_SHEET_COLUMNS_WITH_DATA_FAILED]: ({
		action: { payload },
		dispatch,
		properties: {
			wizardContext: { sheets }
		},
		updateState
	}) => {
		// The columns have failed to parse
		const { completionState, incompleteSheets } = getSheetCompletion(sheets);

		// If there have been any sheets previously completed
		if (completionState === SHEET_COMPLETION_STATES.PARTIAL) {
			// Treat it as a CRITICAL case and close the wizard
			dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', {
				...payload.error,
				detail: t(
					'Unable to import data into new table. Try starting with a template, again.'
				)
			});
			return;
		}

		// If it's the first sheet to be parsed, just display an error and allow the user to try again
		dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
			...payload.error,
			detail: t('Unable to import data into new table.')
		});
		// And finally, if it's a single, bring the user back to the spreadsheet upload page (skipping this page)
		if (incompleteSheets.length === 1) {
			dispatch('SN_GUIDED_WIZARD#PREVIOUS');
		} else {
			// Otherwise set `isConversionInProgress` to false
			updateState({ isConversionInProgress: false });
		}
	},
	[CLEAN_UP_RECORDS]: async ({ properties }) => {
		const {
			cleanUpRecords,
			app: { sys_id: appId }
		} = properties.wizardContext;
		await doCleanUpRecords(cleanUpRecords, appId);
	}
};

createCustomElement('sn-table-wizard-spreadsheet-list-page', {
	view: (state, { dispatch }) => {
		const {
			properties,
			sheetNameToCreate,
			lastCreatedTableName,
			isSingleSheet,
			modalState
		} = state;
		const {
			sheets,
			createdTables,
			fileUpload: { selectedFile },
			defaultPageWidth
		} = properties.wizardContext;

		const {
			incompleteSheets,
			completedSheets,
			completionState
		} = getSheetCompletion(sheets);

		const onComplete = () => {
			if (completionState === SHEET_COMPLETION_STATES.PARTIAL) {
				dispatch('NOW_MODAL#OPENED_SET', { case: 'complete' });
			} else {
				dispatch('SN_GUIDED_WIZARD#COMPLETE', {
					key: 'sys_db_object',
					result: createdTables
				});
			}
		};

		const onEdit = () => {
			if (completionState === SHEET_COMPLETION_STATES.PARTIAL) {
				dispatch('NOW_MODAL#OPENED_SET', { case: 'edit' });
			} else {
				dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
					key: 'sys_db_object',
					result: createdTables
				});
			}
		};

		// `Edit Table(s)` and `Done` buttons should be rendered only if at least one table has been created
		const shouldRenderFooterButtons = completedSheets.length > 0;
		// previous button should be rendered only if *no* tables have been created
		const shouldRenderPrevious = completedSheets.length === 0;

		return (
			<sn-guided-wizard-page
				running-header-text={t('ADD DATA')}
				header-text={headerTextCopy[completionState](lastCreatedTableName)} // Only the `partial` case needs this...
				subheader-text={subheaderTextCopy[completionState]}
				loading={state.isConversionInProgress}
				loading-content={{
					headerText: isSingleSheet
						? t('Preparing your column headers for review')
						: t(
								'Converting the "{0}" sheet into a table and preparing the column headers for review',
								sheetNameToCreate
						  ),
					subheaderText: t(
						'The column headers will display as rows for you to review. You will be able to edit the details of each column.'
					),
					image: parsingIllustrationSVG
				}}
				onPrimaryClick={shouldRenderFooterButtons ? onComplete : null}
				primary-label={t('Done')}
				onSecondaryClick={shouldRenderFooterButtons ? onEdit : null}
				onClose={shouldRenderFooterButtons ? onComplete : null}
				secondary-label={
					completedSheets.length > 1 ? t('Edit tables') : t('Edit table')
				}
				onPrevious={
					shouldRenderPrevious
						? () => dispatch('SN_GUIDED_WIZARD#PREVIOUS')
						: null
				}
				max-width={defaultPageWidth}>
				<now-modal
					opened={modalState.opened}
					manage-opened
					size="md"
					headerLabel={t('Leave without converting other sheets into tables?')}
					footerActions={[
						{
							id: 'primary',
							size: 'md',
							variant: 'primary',
							label: t('Yes, leave')
						},
						{
							id: 'secondary',
							size: 'md',
							variant: 'tertiary',
							label: t('Cancel')
						}
					]}>
					<p>
						{t(
							'If you leave now, you will have to upload the spreadsheet again to convert a worksheet into a table.'
						)}
					</p>
				</now-modal>
				<div className="sheet-list">
					<div
						className="incomplete-sheets"
						hidden={completionState === SHEET_COMPLETION_STATES.COMPLETE}>
						{completedSheets.length > 0 ? (
							<now-heading
								label={t('Remaining sheets')}
								level="2"
								variant="title-primary"
								className="now-m-block-end--xl"
							/>
						) : null}
						{incompleteSheets.map((sheet, index) => {
							const isFirstSheet = index === 0;
							return (
								<sn-tb-spreadsheet-info-card
									expanded={isFirstSheet && !shouldRenderFooterButtons}
									fileName={selectedFile.name}
									sheet={sheet}
								/>
							);
						})}
					</div>
					<div
						className="completed-sheets"
						hidden={completionState === SHEET_COMPLETION_STATES.INCOMPLETE}>
						<hr
							className="completed-divider"
							hidden={completionState === SHEET_COMPLETION_STATES.COMPLETE}
						/>
						<now-heading
							label={
								incompleteSheets.length === 0
									? t('Summary of tables created')
									: t('Tables added to your app')
							}
							level="2"
							variant="title-primary"
							className="now-m-block-end--xl"
						/>
						{completedSheets.map(sheet => {
							return (
								<sn-tb-spreadsheet-info-card
									sheet={sheet}
									fileName={selectedFile.name}
									completed
								/>
							);
						})}
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		// Only used for the `partial` completion state that acknowledges the most recently created table
		lastCreatedTableName: '',
		// Used for displaying the loading content after clicking on a `Convert to Table`
		sheetNameToCreate: '',
		isConversionInProgress: false,
		isSingleSheet: false,
		modalState: { opened: false, case: null }
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers,
	styles
});
