import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import '@servicenow/now-input';
import '@servicenow/now-loader';
import '@servicenow/now-icon';
import 'sn-app-object-wizard-components';
import { t } from 'sn-translate';
import { tablesDataBehavior } from '@devsnc/guided-wizard-toolkit';

const {
	helpers: { fetchExtendableTables },
	constants: {
		EXTENDABLE_TABLES_DATA_PROPERTY,
		EXTENDABLE_TABLES_DATA_LOADING_PROPERTY
	}
} = tablesDataBehavior;

import styles from './sn-table-wizard-extension-page.scss';

import {
	FETCH_COLUMNS,
	FETCH_COLUMNS_SUCCEEDED,
	FETCH_COLUMNS_FAILED
} from './constants';

export const fetchColumnsEffect = createHttpEffect('/api/now/ui/meta/:table', {
	method: 'GET',
	successActionType: FETCH_COLUMNS_SUCCEEDED,
	errorActionType: FETCH_COLUMNS_FAILED,
	batch: false,
	queryParams: ['sysparm_transaction_scope'],
	pathParams: ['table']
});

createCustomElement('sn-table-wizard-extension-page', {
	view: (
		{
			tableToExtend,
			properties: {
				[EXTENDABLE_TABLES_DATA_PROPERTY]: extendableTables,
				[EXTENDABLE_TABLES_DATA_LOADING_PROPERTY]: isLoadingTables = true,
				wizardContext: { table, app, defaultPageWidth }
			}
		},
		{ dispatch }
	) => {
		return (
			<sn-guided-wizard-page
				className="sn-table-wizard-summary-page"
				onPrimaryClick={() => {
					dispatch(FETCH_COLUMNS, {
						table: tableToExtend,
						sysparm_transaction_scope: app.sys_id
					});
				}}
				primary-disabled={!tableToExtend}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onPrevious={() => {
					// Reset dropdown on back in case user changes source type
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', {
						table: { ...table, tableToExtend: '' }
					});
				}}
				running-header-text={t('ADD DATA')}
				headerText={t('Which table do you want to use?')}
				subheaderText={t(
					"We'll add the table's data to your app. Essentially, you are creating an extension with branching logic."
				)}
				max-width={defaultPageWidth}>
				<div>
					{isLoadingTables === false && extendableTables.length > 0 ? (
						<sn-aow-typeahead
							label={t('Table')}
							items={extendableTables}
							selectedItem={tableToExtend}
							variant="tertiary"
							placeholder={t('Select a table from the Now Platform')}
							search="contains"
							iconName="caret-down-fill"
							iconSize="sm"
						/>
					) : (
						<now-loader />
					)}
				</div>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		[EXTENDABLE_TABLES_DATA_PROPERTY]: {
			default: `@tableWizard/${EXTENDABLE_TABLES_DATA_PROPERTY}`
		},
		[EXTENDABLE_TABLES_DATA_LOADING_PROPERTY]: {
			default: `@tableWizard/${EXTENDABLE_TABLES_DATA_LOADING_PROPERTY}`
		}
	},
	initialState: { fetchError: null, tableToExtend: '' }, // `tableToExtend` because `extends` is a reserved key
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch
		}) => {
			fetchExtendableTables(dispatch, wizardContext.app.sys_id);
		},
		'NOW_TYPEAHEAD#SELECTED_ITEM_SET': ({
			action: {
				payload: { value },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ tableToExtend: value });
		},
		[FETCH_COLUMNS]: fetchColumnsEffect,
		[FETCH_COLUMNS_SUCCEEDED]: ({
			action: {
				payload: {
					result: { columns }
				}
			},
			state: { tableToExtend },
			properties: {
				wizardContext: { table }
			},
			dispatch
		}) => {
			const extendedFieldNames = Object.keys(columns);
			const extendedFields = extendedFieldNames.map(fieldName => {
				return {
					name: fieldName,
					label: columns[fieldName].label,
					type: columns[fieldName].type,
					length: columns[fieldName].max_length, // All columns/fields secretly have this property
					...(columns[fieldName].type === 'reference' && {
						reference: columns[fieldName].reference
					})
				};
			});

			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				table: {
					...table,
					tableToExtend,
					extendedFields
				}
			});
		}
	},
	styles
});
