export const SHEET_COMPLETION_STATES = {
	INCOMPLETE: 'incomplete',
	PARTIAL: 'partial',
	COMPLETE: 'complete'
};

/**
 * Three possible states of completion -
 * 1. `none` - no sheets completed
 * 2. `partial` - at least one sheet completed but not all
 * 3. `complete` - all detected sheets completed
 */ 
export const returnCompletionState = (incompleteSheets, completedSheets) => {
	if (incompleteSheets.length && !completedSheets.length) {
		// Has only incomplete sheets
		return SHEET_COMPLETION_STATES.INCOMPLETE;
	} else if (incompleteSheets.length && completedSheets.length) {
		// Has both incomplete and completed sheets
		return SHEET_COMPLETION_STATES.PARTIAL;
	} else if (!incompleteSheets.length && completedSheets.length) {
		// Has only completed sheets
		return SHEET_COMPLETION_STATES.COMPLETE;
	}
};

/**
 * Reduces `sheets` array into an object with `incompleteSheets`(arr),
 * `completedSheets`(arr), and `completionState` (enum)
 * @param {array} sheets 
 */
export const getSheetCompletion = sheets => {
	return sheets.reduce(
		(acc, curSheet) => {
			if (!curSheet.table || !curSheet.table.sys_id) {
				acc.incompleteSheets.push(curSheet);
			} else {
				acc.completedSheets.push(curSheet);
			}
			acc.completionState = returnCompletionState(
				acc.incompleteSheets,
				acc.completedSheets
			);
			return acc;
		},
		{
			incompleteSheets: [],
			completedSheets: [],
			completionState: SHEET_COMPLETION_STATES.INCOMPLETE
		}
	);
};