import { createCustomElement } from '@servicenow/ui-core';

import '../sn-table-choice-input';
import '../sn-table-choice-list';

createCustomElement('sn-table-choices', {
	view: ({ properties: { field } }) => (
		<div className="table-field-card-container">
			<sn-table-choice-input />
			{field.choices.length > 0 ? (
				<sn-table-choice-list choices={field.choices} />
			) : null}
		</div>
	),
	properties: {
		field: {
			default: {}
		}
	}
});
