import { t } from 'sn-translate';
import fetchHelper from './fetch-helper';

const POLLING_INTERVAL = 1000;
const WORKER_STATE_CODE_SUCCESS = 'success';

const poller = {
	wait: () => new Promise(r => setTimeout(r, POLLING_INTERVAL))
};

async function pollingFetch(url, options, isDone) {
	let response;
	while (!(await isDone((response = await fetchHelper(url, options))))) {
		await poller.wait();
	}
	return response;
}

/**
 * Generic poller wait. hits a url and wait using waitCallback
 * @param {string} url
 * @param {function} waitCallback
 */
export async function waitFor(url, waitCallback) {
	const response = await pollingFetch(url, {}, async response => {
		return waitCallback(response);
	});

	return response;
}

/*

Polls and waits for a backend progress worker (sys_progress_worker) to complete.
If the worker does not complete successfully, throw an translated error.

*/
export async function waitForWorker(workerId) {
	const response = await waitFor(
		`/angular.do?sysparm_type=ng_progress&sysparm_progress_id=${workerId}`,
		response => {
			if (response.isError) {
				return true;
			}
			const { state_code } = response;
			return !!state_code;
		}
	);

	const { state_code, isError } = response;

	if (isError || state_code != WORKER_STATE_CODE_SUCCESS) {
		throw { message: t('Worker did not complete successfully') };
	}
}
