import {
	REQUIRED_ERR,
	MIN_CHAR_LIMIT_ONE_ERR,
	NO_PUNCTUATION_ERR,
	NO_DOTS_REGEX_STRING
} from './error-constants';

export const generateStartingNumberError = startingNumber => {
	if (!startingNumber) {
		return REQUIRED_ERR;
	}

	if (+startingNumber < 1) {
		return MIN_CHAR_LIMIT_ONE_ERR;
	}

	const validRegex = new RegExp(NO_DOTS_REGEX_STRING);
	if (!validRegex.exec(startingNumber)) {
		return NO_PUNCTUATION_ERR;
	}

	return null;
};

export const validateDetails = ({ errors }) => {
	for (const key in errors) {
		if (typeof errors[key].message === 'string') {
			return false;
		}
	}

	return true;
};
