import fetchHelper from '../../../helpers/fetch-helper';
import uploadAttachment from '../../../helpers/upload-attachment';
import { HELPER_TABLE } from './constants';

export const createAttachmentHelperRecord = async appId =>
	await fetchHelper(
		`/api/now/table/${HELPER_TABLE}?sysparm_transaction_scope=${appId}`,
		{
			method: 'POST',
			body: JSON.stringify({
				application: appId
			})
		}
	);

export const getSheetNamesAPI = async (attachmentId, appId) =>
	await fetchHelper(
		`/api/sn_table_builder/app/sheet_info/${attachmentId}?sysparm_transaction_scope=${appId}`
	);

/**
 * returns attachmentId and sheets
 * on Error, isError will be true and will have only error response
 * @param {string} attachmentId
 * @param {string} appId
 * @returns {{ isError: boolean, error: Object, sheets: Array.<{ sheetName: string, number: number }> }}
 */
export const getSheetNames = async (attachmentId, appId) => {
	const sheetsResponse = await getSheetNamesAPI(attachmentId, appId);

	if (sheetsResponse.isError) {
		return sheetsResponse;
	}

	return {
		isError: false,
		sheets: transformSheets(sheetsResponse.result)
	};
};

/**
 * Uploads file to helper and give attachmentId and helperRecordId
 * @param {string} appId
 * @param {object} selectedFile
 */
export async function uploadFileToHelper(selectedFile, appId) {
	// Create helper record
	const recordResponse = await createAttachmentHelperRecord(appId);

	if (recordResponse.isError) {
		return recordResponse; // fetchHelper already gives error response
	}

	// Upload file to helper record
	const helperRecordId = recordResponse.result.sys_id;
	const uploadResponse = await uploadToHelper(
		appId,
		helperRecordId,
		selectedFile
	);

	if (uploadResponse.isError) {
		return uploadResponse;
	}

	const { attachmentId } = uploadResponse;

	return {
		isError: false,
		attachmentId,
		helperRecordId
	};
}

async function uploadToHelper(appId, sysId, file) {
	try {
		const {
			result: { sys_id: attachmentId }
		} = await uploadAttachment(
			`/api/now/attachment/upload?sysparm_transaction_scope=${appId}`,
			file,
			{
				table_name: HELPER_TABLE,
				table_sys_id: sysId,
				file_name: file.name
			}
		);
		return { isError: false, attachmentId };
	} catch (msg) {
		return {
			isError: true,
			error: {
				message: msg
			}
		};
	}
}

// transform to support table ui
function transformSheets(sheets) {
	return sheets.map(sheet => ({
		name: sheet.sheetName,
		number: sheet.sheetNumber,
		table: {}
	}));
}
