import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';

import './pages/sn-table-wizard-source-page';
import './pages/sn-table-wizard-spreadsheet-page';
import './pages/sn-table-wizard-spreadsheet-list-page';
import './pages/sn-table-wizard-extension-page';
import './pages/sn-table-wizard-definition-page';
import './pages/sn-table-wizard-details-page';
import './pages/sn-table-wizard-summary-page';

import styles from './sn-table-builder-wizard.scss';

const TABLE_SOURCE_PAGE = 'sn-table-wizard-source-page';
const TABLE_SPREADSHEET_PAGE = 'sn-table-wizard-spreadsheet-page';
const TABLE_SPREADSHEET_LIST_PAGE = 'sn-table-wizard-spreadsheet-list-page';
const TABLE_EXTENSION_PAGE = 'sn-table-wizard-extension-page';
const TABLE_DEFINITION_PAGE = 'sn-table-wizard-definition-page';
const TABLE_DETAILS_PAGE = 'sn-table-wizard-details-page';
const TABLE_SUMMARY_PAGE = 'sn-table-wizard-summary-page';

const pageMap = {
	sourceContinue: {
		spreadsheet: TABLE_SPREADSHEET_PAGE,
		extend: TABLE_EXTENSION_PAGE,
		scratch: TABLE_DETAILS_PAGE
	},
	detailsContinue: {
		spreadsheet: TABLE_SPREADSHEET_LIST_PAGE,
		extend: TABLE_SUMMARY_PAGE,
		scratch: TABLE_SUMMARY_PAGE
	},
	detailsPrevious: {
		spreadsheet: TABLE_DEFINITION_PAGE,
		extend: TABLE_EXTENSION_PAGE,
		scratch: TABLE_SOURCE_PAGE
	}
};

const flowDefinition = {
	startPage: TABLE_SOURCE_PAGE,
	pages: {
		[TABLE_SOURCE_PAGE]: {
			continue: data => pageMap.sourceContinue[data.source]
		},
		[TABLE_SPREADSHEET_PAGE]: {
			continue: () => TABLE_SPREADSHEET_LIST_PAGE,
			previous: () => TABLE_SOURCE_PAGE
		},
		[TABLE_SPREADSHEET_LIST_PAGE]: {
			continue: () => TABLE_DEFINITION_PAGE,
			previous: () => TABLE_SPREADSHEET_PAGE
		},
		[TABLE_EXTENSION_PAGE]: {
			continue: () => TABLE_DETAILS_PAGE,
			previous: () => TABLE_SOURCE_PAGE
		},
		[TABLE_DEFINITION_PAGE]: {
			previous: data =>
				data.sheets.length > 1
					? TABLE_SPREADSHEET_LIST_PAGE
					: TABLE_SPREADSHEET_PAGE,
			continue: () => TABLE_DETAILS_PAGE
		},
		[TABLE_DETAILS_PAGE]: {
			previous: data => pageMap.detailsPrevious[data.source],
			continue: data =>
				data.table.isSingleSheetUpload
					? TABLE_SUMMARY_PAGE
					: pageMap.detailsContinue[data.source]
		},
		[TABLE_SUMMARY_PAGE]: {}
	}
};

const STEPPER_ITEMS = {
	[TABLE_SOURCE_PAGE]: t('Source'),
	[TABLE_DETAILS_PAGE]: t('Define'),
	[TABLE_SUMMARY_PAGE]: t('Summary')
};

const DEFAULT_PAGE_WIDTH = `${770 / 16}rem`;

createCustomElement('sn-table-builder-wizard', {
	view: ({ properties }) => {
		const {
			wizardContext,
			stepperItems,
			templateId,
			parentTemplateInstanceId,
			app,
			user
		} = properties;
		const PageComponent = getCurrentPage(wizardContext);
		return (
			<sn-guided-wizard
				flowDefinition={flowDefinition}
				component-id="tableWizard"
				stepper-items={stepperItems}
				initial-context={{
					templateId,
					parentTemplateInstanceId,
					app,
					user,
					table: {},
					defaultPageWidth: DEFAULT_PAGE_WIDTH
				}}
				tables-data-options={{
					normalizeResult: result => {
						return result.map(table => {
							return {
								id: table.name,
								label: `${table.label} [${table.name}]`,
								is_extendable: table.is_extendable
							};
						});
					}
				}}>
				<PageComponent wizardContext={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		wizardContext: { default: '@tableWizard/wizardContext' },
		templateId: { default: 'c3d2224558512010fa9be831f6272862' },
		parentTemplateInstanceId: { default: null },
		app: { default: {} },
		user: { default: { roles: [] } }
	},
	behaviors: [
		{
			behavior: stepperBehavior.behavior,
			options: {
				config: STEPPER_ITEMS
			}
		}
	],
	styles
});
